import React, { useEffect, useState } from "react";
import LazyLoad from "react-lazyload";
import "./index.css";
import userVideo from "../../../assets/Video_suggest.png";
import { useSelector } from "react-redux";
const UserVideo = ({ handleTracking }) => {
  const [listDataBannerLocal, setListDataBannerLocal] = useState([]);
  const bookMarkState = useSelector((state) => state.bookmark);
  const { listDataBanner } = bookMarkState;

  useEffect(() => {
    if (
      Array.isArray(listDataBanner) &&
      listDataBanner.length <= 0 &&
      localStorage.getItem("bookMarkState") !== null
    ) {
      let getBannerLocal = JSON.parse(
        localStorage.getItem("bookMarkState")
      ).listDataBanner;
      setListDataBannerLocal(getBannerLocal);
    }
  }, []);

  return (
    <div className="ext__video--suggest">
      {(listDataBanner.length > 0 ? listDataBanner : listDataBannerLocal).map(
        (item, index) => {
          return (
            <a href={item.link}>
              <div key={index} onClick={() => handleTracking("searchBar")} className="ext__video--suggest--click">
                <LazyLoad>
                  <img
                    src={item.image}
                    alt="mochi video"
                    className="ext__video--suggest--image"
                  />
                </LazyLoad>
                <div className="ext__video--title">
                  <p>{item.text}</p>
                </div>
              </div>
            </a>
          );
        }
      )}
    </div>
  );
};

export default UserVideo;
