import * as types from "./types";

const INITIAL_STATE = {
  usWord: [],
  vnWord: [],
  suggest: [],
  wordToSearch: '',
  enParagraph: '',
  translatedParagraph: "",
  firstSearch:'',
  searchPosition: '',
  loading: false
};

const reducer = (state = INITIAL_STATE,action) => {
  
  const payload = action.payload;
  switch (action.type) {
    case types.SEARCH_WORD:
      return {
        ...state,
        usWord: payload.us,
        vnWord: payload.vi,
        suggest: payload.suggests,
        wordToSearch: action.key,
      };
    case types.SEARCH_PARAGRAPH:
      return {
        ...state,
        enParagraph: payload.en,
        translatedParagraph: payload.vi,
      };
    case types.SET_LOADING:
      return {
        ...state,
        loading: payload,
      };
    case types.FIRST_SEARCH:
      return {
        ...state,
        firstSearch: payload.first_search,
        searchPosts: payload.search_position
      }
    default:
      return state;
  }
};

export default reducer;
