import * as types from "./types";

const INITIAL_STATE = {
  userToken: "",
  backgroundId: window.localStorage.getItem("selectedBg")
    ? window.localStorage.getItem("selectedBg")
    : 0,
  profileLoading: false,
  displayName: "Mochi",
  email: "mochi@gmail.com",
  avatar: "",
  expiredDay: "",
  startDay: "",
  totalWord: 0,
  reviewCount: 0,
  statistic: [
    {
      id: 1,
      total: 0,
    },
    {
      id: 2,
      total: 0,
    },
    {
      id: 3,
      total: 0
    },
    {
      id: 4,
      total: 0,
    },
    {
      id: 5,
      total: 0,
    },
  ],
  reviewTime: "",
  userIp: ''
};

const reducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case types.FETCH_PROFILE:
      return {
        ...state,
        userToken: action.payload,
      };
    case types.BACKGROUND_ID:
      return {
        ...state,
        backgroundId: action.payload,
      };
    case types.FETCH_PROFILE_SUCCESS:
      return {
        ...state,
        displayName: action.payload.display_name,
        email: action.payload.email,
        avatar: action.payload.avatar,
        expiredDay: action.payload.expired_day,
        startDay: action.payload.created_at,
      };
    case types.FETCH_STATISTIC_SUCCESS:
      return {
        ...state,
        totalWord: action.payload.total,
        reviewCount: action.payload.review_count,
        reviewTime: action.payload.review_time,
        statistic: convertStatistic(state.statistic,action.payload.statistic),
      };

    default:
      return state;
  }
};

export default reducer;
function convertStatistic(statistic,data) {
  let convertArray = [...statistic];
  let countFiveAndMore = 0;
  let countFourAndMore = 0;

  data.forEach((item, index) => {
    if (item.proficiency < 4) {
      let indexStatic = null;
      let itemStatic = {};
      convertArray.forEach((ele,id) => {
        if (ele.id == item.proficiency) {
          itemStatic = { id: item.proficiency,total: item.count };
          indexStatic = id;
        }
      });
      if (Object.keys(itemStatic).length > 0) {
        convertArray[indexStatic] = itemStatic;
      }
    } else if (item.proficiency == 4 || item.proficiency == 5) {
      countFourAndMore += item.count;
    } else {
      countFiveAndMore += item.count;
    }
  });
    convertArray[3] = {
      id: 4,
      total: countFourAndMore,
    };
    convertArray[4] = {
      id: 5,
      total: countFiveAndMore,
    };
  
  return convertArray;
}
