/*global chrome*/

import "../component.css";
import "../GeneralPage/general.css";
import clock from "../../assets/clock.png";
import mascotGuide from "../../assets/Mochi-chi-dan.png";
import defaultAva from "../../assets/default-avatar.png";

import settings from "../../assets/settings.png";
import notice from "../../assets/FAQ.png";
import ellipse from "../../assets/Ellipse.png";
import MainButton from "../commons/Button";
import { useEffect, useState } from "react";

import { Col, Row, OverlayTrigger, Tooltip } from "react-bootstrap";
import Clock from "react-live-clock";

import SettingModal from "./SettingModal";
import LoginModal from "./LoginModal";
import { useSelector } from "react-redux";
import EventModal from "./EventModal";
import NoticeModal from "./NoticeModal";
import SlideModal from "./SlideModal";
import NumberNotice from "./NumberNotice";
import iconFacebook from "../../assets/Mochien_app_icon.png";
import HistoryRow from "../SecondRow/History/HistoryRow";

// fake data default
const mockData = [
  {
    image: iconFacebook,
    title: "Mochi Mochi",
    url: "https://learn.mochidemy.com/",
  },
  {
    image: iconFacebook,
    title: "Mochi Mochi",
    url: "https://learn.mochidemy.com/",
  },
  {
    image: iconFacebook,
    title: "Mochi Mochi",
    url: "https://learn.mochidemy.com/",
  },
  {
    image: iconFacebook,
    title: "Mochi Mochi",
    url: "https://learn.mochidemy.com/",
  },
];

function FirstRow({ getDataTopBar }) {
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [showSettingModal, setShowSettingModal] = useState(false);
  const [showEventModal, setShowEventModal] = useState(false);
  const [showBlurBg, setShowBlurBg] = useState(false);
  const [isSlideModal, setIsSlideModal] = useState(false);
  const [isUrlHistory, setUrlHistory] = useState(mockData);
  const [isDataVideoHistory, setDataVideoHistory] = useState([]);
  const [isBookMark, setBookMark] = useState([]);
  const [isSidebar, setSidebar] = useState([]);
  const [isBannerVideo, setBannerVideo] = useState([]);
  const [isListVideos, setListVideos] = useState([]);
  const [isSuccess, setSuccess] = useState(false);

  const accountState = useSelector((state) => state.account);
  const { displayName, avatar, userToken, email, expiredDay } = accountState;
  // console.log("dulieu", { displayName, avatar, userToken, email, expiredDay } );
  // console.log("test local", window.localStorage.getItem("profileToken"));
  useEffect(() => {
    chrome.runtime.sendMessage("getUrlHistory", function (response) {
      console.log("----------res--------------", response);
      let getDataHistory = setInterval(() => {
        if (response) {
          chrome.storage.local.get(["convertText"], function (result) {
            console.log("<<<<<<< ket qua >>>>>>>>", result);
            if (result.convertText !== undefined) {
              console.log("result", result.convertText);
              if (result.convertText.length >= 4) {
                setUrlHistory(result.convertText);
                localStorage.setItem(
                  "listUrlHistory",
                  JSON.stringify(result.convertText)
                );
                chrome.runtime.sendMessage("clearHistory");
                clearInterval(getDataHistory);
              }
            } else {
              setUrlHistory(mockData);
            }
          });
        }
      }, 1000);
    });
    // return () => {
    //   setUrlHistory([]);
    // }
  }, []);

  const handleOpenLogin = () => {
    setShowLoginModal(!showLoginModal);
  };
  const handleCloseSlide = (type) => {
    // console.log("type", type);
    setIsSlideModal(type);
  };
  const handleOpenSlide = () => {
    setIsSlideModal(true);
    setShowEventModal(!showEventModal);
    chrome.runtime.sendMessage("clickNotice");
  };
  // Lấy dữ liệu từ localStorage
  const handleBlur = () => {
    setIsSlideModal(false);
  };
  let resultNumber = localStorage.getItem("dataEvent");
  let numberArray = JSON.parse(resultNumber);

  const handleOpenLoginModel = () => {
    window.localStorage.setItem("buttonLogin", "1");
    chrome.runtime.sendMessage("modalLoginButton");
    setShowLoginModal(!showLoginModal);
  };
  const handleTracking = (index ,position) => {
    console.log("----index----", index);
    if (typeof index === "number" && position === "bookMark") {
      chrome.runtime.sendMessage(`clickBookMark${index}`);
    } else if ( index === "searchBar" ) {
      chrome.runtime.sendMessage("clickSearchBar");
    } else if ( typeof index === "number" && position === 'infeed' ) {
      chrome.runtime.sendMessage(`clickInFeed${index}`);
    }
  };
  return (
    <>
      {isSlideModal && numberArray.length > 1 ? (
        <div
          className="bgc-overlay"
          onClick={() => handleCloseSlide(false)}
        ></div>
      ) : (
        ""
      )}
      <Row className="first-row">
        <Col md={5} lg={3} sm={8}>
          <div className="date_time">
            <div className="clock-wrapper">
              <img src={clock} alt="clock" />
              <Clock
                className="clock"
                format={"HH:mm"}
                ticking={true}
                interval={60000}
                timezone={"Asia/Ho_Chi_Minh"}
              />
            </div>
            <Clock
              className="date"
              format={"dddd, MMMM Do, YYYY"}
              timezone={"Asia/Ho_Chi_Minh"}
            />
          </div>
        </Col>
        <Col lg={6} md={12} sm={12}>
          <HistoryRow
            isUrlHistory={isUrlHistory}
            isBookMark={isBookMark}
            isSuccess={isSuccess}
            handleTracking={handleTracking}
          />
        </Col>
        <Col
          sm={{ span: 5, offset: 7 }}
          md={{ span: 5, offset: 7 }}
          lg={{ span: 3, offset: 0 }}
          className="button-group-wrapper"
        >
          <div className="button-group">
            <div className="btn__number--notice">
              <OverlayTrigger
                placement="bottom"
                overlay={<Tooltip id="settings-tooltip">Thông báo</Tooltip>}
              >
                <img
                  src={notice}
                  alt="notice"
                  className="notice-icon"
                  onClick={handleOpenSlide}
                />
              </OverlayTrigger>
              <NumberNotice />
            </div>
            {(window.localStorage.getItem("profileToken") !== null &&
              userToken) ||
            window.localStorage.getItem("profileToken") !== null ? (
              <OverlayTrigger
                placement="bottom"
                // delay={{ show: 100, hide: 200 }}
                overlay={
                  <Tooltip id="avatar-tooltip">
                    <div className="avatar-tooltip">
                      <p>
                        <strong>
                          {expiredDay ? "Premium Account" : "Free Account"}
                        </strong>
                      </p>
                      <p>{displayName}</p>
                      <p>{email}</p>
                    </div>
                  </Tooltip>
                }
              >
                <div className={`avatar-wrapper`}>
                  <img
                    src={avatar ? avatar : defaultAva}
                    alt="avatar"
                    className={`user-avatar ${
                      expiredDay ? "paid-bg" : "free-bg"
                    }`}
                    onClick={() => {
                      window.open(
                        `https://mochidemy.com/extension-page-login/`
                      );
                    }}
                  />
                </div>
              </OverlayTrigger>
            ) : (
              <MainButton onClick={handleOpenLoginModel} className="loginBtn">
                Đăng nhập
              </MainButton>
            )}
            <OverlayTrigger
              placement="bottom"
              // delay={{ show: 100, hide: 200 }}
              overlay={<Tooltip id="settings-tooltip">Cài Đặt</Tooltip>}
            >
              <img
                src={settings}
                alt="settings"
                className="setting-icon"
                onClick={() => setShowSettingModal(true)}
              />
            </OverlayTrigger>
          </div>
        </Col>
      </Row>
      {/* Login Modal */}
      <LoginModal
        show={showLoginModal}
        mascot={mascotGuide}
        handleClose={handleOpenLogin}
      ></LoginModal>
      {/* Setting Modal */}
      {showBlurBg && <div className="blur-bg"></div>}

      <SettingModal
        show={showSettingModal}
        handleClose={() => setShowSettingModal(!showSettingModal)}
        handleShowLogin={() => {
          setShowSettingModal(false);
          setShowLoginModal(true);
        }}
        setShowBlurBg={(value) => setShowBlurBg(value)}
      ></SettingModal>
      {
        // Event Modal
        (numberArray && numberArray.length === 0) ||
        (numberArray && numberArray.length === null) ? (
          <NoticeModal
            show={showEventModal}
            handleClose={() => setShowEventModal(!showEventModal)}
          />
        ) : (
          ""
        )
      }
      {numberArray && numberArray.length === 1 ? (
        <EventModal
          show={showEventModal}
          handleClose={() => setShowEventModal(!showEventModal)}
        />
      ) : (
        ""
      )}
      {numberArray && numberArray.length > 1 && isSlideModal ? (
        <SlideModal
          onBlur={handleBlur}
          slide={setIsSlideModal}
          close={handleCloseSlide}
          status={isSlideModal}
        ></SlideModal>
      ) : (
        ""
      )}
      {/* Event Modal */}

      {/* <SlideModal></SlideModal> */}
    </>
  );
}

export default FirstRow;
