import React, { useEffect, useState } from "react";
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import "./index.css";
import AppIconCircle from "../../assets/App_icon_circle.png";
import { useSelector } from "react-redux";
const LeftNewTab = ({ handleTrackingSideBar }) => {
  const [listDataSideBarLocal, setSidebarLocal] = useState([]);
  const bookMarkState = useSelector((state) => state.bookmark);

  const { listDataSideBar } = bookMarkState;
  useEffect(() => {
    if (Array.isArray(listDataSideBar) && listDataSideBar.length <= 0 &&  localStorage.getItem("bookMarkState") !== null ) {
      const listDataSideBarLocal = JSON.parse(
        localStorage.getItem("bookMarkState")
      ).listDataSideBar;
      setSidebarLocal(listDataSideBarLocal);
    }
  }, [])
  return (
    <div className="ext__left--new-tab">
      {(listDataSideBar.length > 0
        ? listDataSideBar
        : listDataSideBarLocal
      ).map((item, index) => {
        return (
          <OverlayTrigger
            placement="right"
            overlay={item.text ? <Tooltip>{item.text}</Tooltip> : <></>}
          >
            <div key={index} onClick={() => handleTrackingSideBar(index)}>
              <a href={item.link}>
                <img src={item.image} alt="" />
              </a>
            </div>
          </OverlayTrigger>
        );
      })}
    </div>
  );
};

export default LeftNewTab;
