export const searchWord = (params, success = () => {}, fail = () => {}) => {
  return (dispatch) => {
    client
      .get("v3.1/words/dictionary-english", { params: params })
      .then((response) => {
        const result = response.data;
        if (result.code == "1") {
          success(true);
          dispatch(actions.searchWord(result.data, params.key));
          dispatch(actions.firstSearch(result.first_search,params.key));
        }
      })
      .catch((error) => {
        const errorMsg = error.message;
        console.log({ errorMsg });
        // dispatch(actions.fetchListFailed(errorMsg));
      });
  };
};
export const searchParagraph = (params,success = () => { },fail = () => { }) => {
  return async (dispatch) => {
     dispatch(actions.setLoading(true));
  await fetch(
    "https://translate.googleapis.com/translate_a/single?client=dict-chrome-ex&sl=en&tl=vi&hl=en-US&dt=t&dt=bd&dj=1&source=bubble&q=" +
      encodeURIComponent(params.q),
    {}
  )
    .then((response) => response.json())
    .then((response) => {
      const sentences = response.sentences;
      if (Array.isArray(sentences) && sentences.length > 0) {
        const translatedParagraph = sentences
          .map((item) => item.trans)
          .join("");
        if (translatedParagraph) {
          dispatch(
            actions.searchWordParagraph({
              en: params.q,
              vi: translatedParagraph,
            })
          );
          success(true);
          dispatch(actions.setLoading(false));
        }
      }
    });
}
};
export const saveWord = (params, success = () => {}, fail = () => {}) => {
  
  return (dispatch) => {
    
    client
      .post("v3.1/words/dictionary-english/add-word", params)
      .then((response) => {
        const result = response.data;
        if (result.code != "0") {
          success(result);
          //   dispatch(actions.searchWord(result.data, params.key));
        }
      })
      .catch((error) => {
        const errorMsg = error.message;
        console.log({ errorMsg });
        // dispatch(actions.fetchListFailed(errorMsg));
      });
  };
};

function getCookies(domain, name, callback) {
  chrome.cookies.get({ url: domain, name: name }, function (cookie) {
    if (callback) {
      callback(cookie.value);
    }
  });
}
function getCookieLocal() {
  const mochiTokenCookie = document.cookie.split("mochiToken=");
  if (mochiTokenCookie.length === 2) {
    return mochiTokenCookie[1].split(";")[0];
  } else {
    return "";
  }
}
export default { searchWord, saveWord, searchParagraph };
