/*global chrome*/

import "../component.css";
import checkIcon from "../../assets/check.png"
import plusIcon from "../../assets/plus.png";
import mascotGuide from "../../assets/Mochi-chi-dan.png";

import unCheckIcon from "../../assets/uncheck.png";

import { Col, Row } from "react-bootstrap";
import { useState, useEffect } from "react";
import LoginModal from "./LoginModal";
import { useSelector } from "react-redux";

function QuoteContent(props) {
  const [showLoginModal, setShowLoginModal] = useState(false);
const accountState = useSelector((state) => state.account);
const { userToken } = accountState;
  let selectedQuote = window.localStorage.getItem("selectedQuote");
  if (selectedQuote) {
    selectedQuote = JSON.parse(selectedQuote);
  } else {
    let tempList = JSON.stringify([
      {
        id: 0,
        active: true,
      },
      {
        id: 1,
        active: true,
      },
      {
        id: 2,
        active: true,
      },
      {
        id: 3,
        active: true,
      },
    ]);
    window.localStorage.setItem("selectedQuote", tempList);
  }
  const [activeQuote, setActiveQuote] = useState(selectedQuote);
  const [listQuote, setListQuote] = useState([]);

  const listQuotes = [
    {
      content:
        "Practice does not make perfect. Only perfect practice makes perfect.",
      id: 0,
    },
    {
      content: "Don't stop when you're tired, stop when you're done.",
      id: 1,
    },
    {
      content: "The best way to predict the future is to create it.",
      id: 2,
    },
    {
      content: "Don't dream of winning. Train for it!",
      id: 3,
    },
  ];
  useEffect(() => {
    let listActiveQuote = listQuotes.map((item, i) => Object.assign({}, item, activeQuote[i]))
    setListQuote(listActiveQuote);
  },[])
  useEffect(() => {
    let listActiveQuote = [];
    const tempArr = listQuotes.map((item, i) =>
      Object.assign({}, item, activeQuote[i])
    );
    tempArr.forEach((item) => {
      if (item.active) {
        listActiveQuote.push(item);
      }
    });
    tempArr.forEach((item) => {
      if (!item.active) {
        listActiveQuote.push(item);
      }
    });
    setListQuote(listActiveQuote);
  }, [activeQuote]);

  const handleCheck = (id,oldStatus) => {
    const tempArr = [...activeQuote]
    const newActiveList = tempArr.map(item => {
      if (item.id === id) {
        return {
          id: item.id,
          active: !oldStatus,
        }
      }
      else {
        return item;
      }
    })
      window.localStorage.setItem(
        "selectedQuote",
        JSON.stringify(newActiveList)
      );

    setActiveQuote(newActiveList);
  };
  const handleAddQuote = () => {
    if (userToken) {
      window.open("https://forms.gle/Z29FP584bgheH9rC7", "_blank");
    }
    else {
      setShowLoginModal(true)
      props.setShowBlurBg(true);
    }
  }
  return (
    <>
      <Row>
        <Col md={12}>
          <h4>Quotes</h4>
        </Col>
      </Row>
      <Row className="quote-content">
        <h5>Mochi Quotes</h5>
        <div className="list-quote">
          {listQuote.map((item) => {
            return (
              <div className="quote-item-wrapper" key={item.id}>
                {item.active ? (
                  <img
                    src={checkIcon}
                    alt="check"
                    className="quote-check"
                    onClick={() => handleCheck(item.id, true)}
                  />
                ) : (
                  <img
                    src={unCheckIcon}
                    alt="uncheck"
                    className="quote-check"
                    onClick={() => handleCheck(item.id, false)}
                  />
                )}
                <p className="quote-item">{item.content}</p>
              </div>
            );
          })}
        </div>
        <div className="contact-wrapper">
          <h5>Quotes của bạn</h5>
          <div className="contact-item">
            <img
              src={plusIcon}
              alt="plus"
              className="quote-check"
              onClick={handleAddQuote}
            />
            <p onClick={handleAddQuote}>Thêm quote của bạn</p>
          </div>
        </div>
        <LoginModal
          show={showLoginModal}
          mascot={mascotGuide}
          handleClose={() => {
            props.setShowBlurBg(false)
            setShowLoginModal(false)
          }}
          className="login-modal-in-modal"
        ></LoginModal>
      </Row>
    </>
  );
}

export default QuoteContent;
