/*global chrome*/

import "../component.css";
import cross from '../../assets/cross.png'
import {Modal} from 'react-bootstrap'
import { useState, useRef, useEffect } from "react";
function MainModal(props) {
  // alert(props.className);
  return (
    <div onClick={props.handleClose}>
      <Modal
        {...props}
        // size="sm"
        // show={show}
        aria-labelledby="contained-modal-title-vcenter"
        centered
        backdrop="true"
        onClick={(e) => {
          e.stopPropagation();
        }}
      >
        {props.mascot && (
          <img
            src={props.mascot}
            alt="mascot"
            className={`modal-mascot ${
              props.mascotClass ? props.mascotClass : ""
            }`}
          />
        )}

        <img
          src={cross}
          alt="mascot"
          className={
            props.className === "modal_notify_save" || 
            props.className === "modal_notify_save_over_TDV" ||
            props.className === "login-modal-in-modal" ||
            props.className === undefined
            ? `modal-close-btn modal-close-popup` 
            : `modal-close-btn`}
          onClick={props.handleClose}
        />

        <div className="modal-children">{props.children}</div>
      </Modal>
    </div>
  );
}

export default MainModal;
