import * as actions from "./actions";
import { ApiClient } from "../../../AppClient";
let client = new ApiClient();

const handleGetBookMark = (params, success = () => {}, fail = () => {}) => {
  return (dispatch) => {
    client
      .get("/v3.1/extension-new-tab", { params: params })
      .then((response) => {
        const result = response.data;
        const resultData = result.data;
        if (result.code === 1) {
          resultData.forEach((item, index) => {
            switch (item.position) {
              case 1:
                dispatch(actions.handleBookMark(item.data));
                break;
              case 2:
                dispatch(actions.handleSideBar(item.data));
                break;
              case 3:
                dispatch(actions.handleBanner(item.data));
                break;
              case 4:
                dispatch(actions.handleInfeed(item.data));
                break;
              default:
                return;
            }
          });
        }
      })
      .catch((error) => {
        const errorMsg = error.message;
        console.log({ errorMsg });
        // dispatch(actions.fetchListFailed(errorMsg));
      });
  };
};
export default { handleGetBookMark };
