import React from "react";
import InfoHistory from "./InfoHistory";
import "./index.css";
import HistoryLeft from "./HistoryLeft";
import HistoryRight from "./HistoryRight";
const HistoryRow = ({ isUrlHistory, isBookMark, isSuccess, handleTracking }) => {
  return (
    <div className="mochi__history">
      <HistoryLeft
        className="mochi__history--border"
        isUrlHistory={isUrlHistory}
      />
      <HistoryRight isUrlHistory={isUrlHistory} isBookMark={isBookMark} handleTracking={handleTracking} />
    </div>
  );
};

export default HistoryRow;
