/*global chrome*/

import "../component.css";
import MainModal from "../commons/MainModal";
import noEvent from "../../assets/nopopup.png";

function NoticeModal(props) {
  return (
    <MainModal {...props} size="lg" className="setting-modal-wrapper">
      <div className="notice__no--popup">
        <img 
            src = {noEvent}
            alt = "no event"
            className="image__no--event"
        />
        <p style={
            {
                textAlign: "center",
                marginTop: "5%",
                fontWeight: "600",
                fontStyle: "normal",
                fontFamily: "Quicksand",
            }
        }> Hiện không có sự kiện nào.<br/> 
            Quay lại sau để cập thông tin các sự kiện mới <br/>
            của MochiMochi nhé ^^
        </p>
      </div>
    </MainModal>
  );
}

export default NoticeModal;
