/*global chrome*/

import "./index.css";

import mochiIcon from "../../assets/logo_orange.png";
import exitIcon from "../../assets/button_exit.png";
import PrimaryButton from "../commons/PrimaryButton";
import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import EVResult from "./EVResult";
import EEResult from "./EEResult";
import { Row, Col, Spinner } from "react-bootstrap";
const ORIGIN = process.env.REACT_APP_ORIGIN;

function ResultModalPara({ setShowResultParagraph, handleSearchSuggest }) {

  const wordState = useSelector((state) => state.word);
  useEffect(() => {
    chrome.runtime.sendMessage("tracking-word-new-tab");
  },[])

  const { enParagraph, loading, translatedParagraph } = wordState;
  return (
    <div className="search-result-wrapper">
      <img
        src={exitIcon}
        alt="exit icon"
        className="exit-icon"
        onClick={() => setShowResultParagraph(false)}
      />
      <div className="dict-select-wrapper">
        <div className={`dict-tab selected`}>Anh - Việt</div>
      </div>
      <div className="result-main">
        {loading ? (
          <div className="paragraph-loading">
            <Spinner animation="border" variant="warning" />
          </div>
        ) : (
          <div className="result-wrapper-paragraph">
            <div className="result-paragraph en-paragraph">
              <p> {enParagraph}</p>
            </div>
            <div className="result-paragraph vi-paragraph">
              <p> {translatedParagraph}</p>
            </div>
          </div>
        )}
      </div>
      <Row className="result-footer">
        <Col md={{ span: 12 }}>
          <img src={mochiIcon} alt="mochi icon" className="mochi-icon" />
        </Col>
      </Row>
    </div>
  );
}

export default ResultModalPara;
