import React from "react";
import InfoHistory from "./InfoHistory";
import "./index.css";

const HistoryLeft = ({ isUrlHistory }) => {
  const handleImageHistory = (url) => {
  }
  const getDataHistory = JSON.parse(localStorage.getItem("listUrlHistory")) !== null ? JSON.parse(localStorage.getItem("listUrlHistory")) : isUrlHistory ;
  return (
    <div className="ext__history--left mochi__history--border">
      {getDataHistory.map((item, index) => {
        return (
          <div key={index} className="ext__history--list--item">
            <a href={item.url}>
              <InfoHistory urlImage={item.image} titleHistory={item.title} />
            </a>
          </div>
        );
      })}
    </div>
  );
};

export default HistoryLeft;
