/*global chrome*/

import "../component.css";
import chromeIcon from "../../assets/chrome.png";

import { Col, Row, Carousel, OverlayTrigger, Tooltip } from "react-bootstrap";
let selectedQuote = window.localStorage.getItem("selectedQuote");
if (selectedQuote) {
  selectedQuote = JSON.parse(selectedQuote);
} else {
  let tempList = JSON.stringify([ 
    {
      id: 0,
      active: true,
    },
    {
      id: 1,
      active: true,
    },
    {
      id: 2,
      active: true,
    },
    {
      id: 3,
      active: true,
    },
  ]);
  window.localStorage.setItem("selectedQuote", tempList);
}
function ThirdRow() {
  const listQuotes = [
    {
      content:
        "",
      id: 0,
    },
    {
      content: "Don't stop when you're tired, stop when you're done.",
      id: 1,
    },
    {
      content: "The best way to predict the future is to create it.",
      id: 2,
    },
    {
      content: "Don't dream of winning. Train for it!",
      id: 3,
    },
  ];
  let listActiveQuotes = [];
  if (selectedQuote && selectedQuote.length > 0) {
    listQuotes.forEach((item) => {
      const findQuote = selectedQuote.find((quote) => quote.id == item.id);
      if (findQuote && findQuote.active) {
        listActiveQuotes.push(item);
      }
    });
  }
  else {
    listActiveQuotes = [...listQuotes];
  }

  const handleOpenChromeDefault = () => {
    chrome.tabs.update({
      url: "chrome-search://local-ntp/local-ntp.html",
    });
  };
  return (
    <>
      <Row style={{ alignItems: "center" }}>
        {/* <Col md={2} sm={2} className="chrome-icon-wrapper">
          <OverlayTrigger
            placement="top"
            // delay={{ show: 100, hide: 200 }}
            overlay={<Tooltip id="chrome-icon-tooltip">Chrome New Tab</Tooltip>}
          >
            <img
              src={chromeIcon}
              alt="chrome"
              className="chrome-icon"
              onClick={handleOpenChromeDefault}
            />
          </OverlayTrigger>
        </Col> */}
        <Col
          md={{ span: 9, offset: 3 }}
          sm={{ span:9, offset: 3 }}
          className="quote-wrapper"
        >
          <div className="">
            <Carousel controls={false} indicators={false}>
              {listActiveQuotes.length > 0 &&
                listActiveQuotes.map((item) => {
                  return (
                    <Carousel.Item interval={3600000} key={item.id}>
                      <p className="quote-content">
                        <strong>{item.content}</strong>
                      </p>
                    </Carousel.Item>
                  );
                })}
            </Carousel>
          </div>
        </Col>
      </Row>
    </>
  );
}

export default ThirdRow;
