/*global chrome*/

import "./index.css";
import ReactGA from "react-ga";
import mochiSave from "../../assets/Mochi-save-word.png";
import mochiNoti from "../../assets/Mochi-thong-bao.png";
import mochiTDV from "../../assets/Mochi-meomeo2.png";
import mochiOver from "../../assets/Mochi-chi-dan.png";
import PrimaryButton from "../commons/PrimaryButton";
import { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { wordOperations } from "../../state/modules/word";
import { Col, Row } from "react-bootstrap";
import MainModal from "../commons/MainModal";
const trackingURL = process.env.REACT_APP_TRACKING_URL;
function NotifyModal({ type, setShowModal }) {
  // ReactGA.initialize("UA-150440250-2",{
  //   gaOptions: { cookieDomain: "none" },
  // });
  // const ga = ReactGA.ga();
  // ga("set","checkProtocolTask",function () {
  //   /* nothing */
  // });

  const [overLoad, setOverLoad] = useState(false);

  const accountState = useSelector((state) => state.account);

  const { expiredDay, statistic, reviewCount, reviewTime, email } = accountState;
  const reviewTimeObject = new Date(reviewTime);
  const now = new Date();

  const isBeforeTDV = reviewTimeObject.getTime() - now.getTime() > 0;

  const dispatch = useDispatch();
  const handleOpenWebsite = async () => {
    setShowModal("");
    window.open("https://learn.mochidemy.com/?source=extension&category=extension_browser&action=click_button&label=add_new_words", "_blank");
    // const useIp = await getIp();
    // await fetch(
    //   `${trackingURL}?action=newTab_open_website&ip=${useIp}&email=${email}`
    // )
    //   .then((response) => response.json())
    //   .then((data) => {
    //     if (data.result == "success") {
    //       console.log({ data });
    //     }
    //   });
  };
  const handleUpgrade = async () => {
    chrome.runtime.sendMessage("checkClickUpgrade");
    setShowModal("");
    window.open("https://mochidemy.com/extension?source=extension&category=extension_browser&action=click_button&label=upgrade_premium");
  };

  return (
    <>
      {/* Lưu từ thành công - Trước thời điểm vàng */}
      {isBeforeTDV && (
        <MainModal
          show={type == "normal"}
          mascot={mochiTDV}
          handleClose={() => setShowModal("")}
        >
          <Row className="notify_content">
            <Col md={12}>
              <div>
                <p className="notify-save-word">Bạn đã lưu từ thành công.</p>
                <p className="notify-save-word">
                  Đừng quên ôn tập từ vào Thời Điểm Vàng để tăng hiệu quả ghi
                  nhớ
                </p>
                <PrimaryButton
                  onClick={() => {
                    setShowModal("");
                  }}
                  className="btn_save_notify"
                >
                  Mình đã hiểu
                </PrimaryButton>
              </div>
            </Col>
          </Row>
        </MainModal>
      )}
      {/* Lưu từ thành công - Sau thời điểm vàng */}
      {!isBeforeTDV && (
        <MainModal
          show={type == "normal"}
          mascot={mochiSave}
          handleClose={() => setShowModal("")}
          className="modal_notify_save_over_TDV"
        >
          <Row className="notify_content">
            <Col md={12}>
              <div>
                <p className="notify-save-word">Bạn đã lưu từ thành công.</p>
                <p className="notify-save-word">
                  Đăng nhập vào website và mở tab SỔ TAY để xem từ vựng vừa lưu
                </p>
                <PrimaryButton
                  onClick={handleOpenWebsite}
                  className="btn_save_notify"
                >
                  Mở website ngay
                </PrimaryButton>
              </div>
            </Col>
          </Row>
        </MainModal>
      )}

      <MainModal
        show={type == "free-over-50lv1"}
        mascot={mochiOver}
        handleClose={() => setShowModal("")}
        className="modal_notify_save_over_TDV"
      >
        {/* Free user */}

        <Row className="notify_content">
          <Col md={12}>
            <div>
              <p className="notify-save-word">
                Bạn đang có hơn 50 từ vựng level 1. Ôn tập ngay để lưu thêm từ
                vào sổ tay
              </p>
              <PrimaryButton
                onClick={() => {
                  setShowModal("");
                  window.open("https://learn.mochidemy.com/user/review?");
                }}
                className="btn_save_notify"
              >
                Ôn tập ngay
              </PrimaryButton>
            </div>
          </Col>
        </Row>
      </MainModal>

      <MainModal
        show={type == "paid-over-100-lv1"}
        mascot={mochiOver}
        handleClose={() => setShowModal("")}
        className="modal_notify_save_over_TDV"
      >
        <Row className="notify_content">
          <Col md={12}>
            <div>
              <p className="notify-save-word">
                Bạn đang có hơn 100 từ vựng level 1.
              </p>
              <p className="notify-save-word">
                Ôn tập ngay để lưu thêm từ vào sổ tay
              </p>
              <PrimaryButton
                onClick={() => {
                  setShowModal("");
                  window.open("https://learn.mochidemy.com/user/review?");
                }}
                className="btn_save_notify"
              >
                Ôn tập ngay
              </PrimaryButton>
            </div>
          </Col>
        </Row>
      </MainModal>

      <MainModal
        show={type == "paid-over-100"}
        mascot={mochiOver}
        handleClose={() => setShowModal("")}
        className="modal_notify_save_over_TDV"
      >
        <Row className="notify_content">
          <Col md={12}>
            <div>
              <p className="notify-save-word">
                Bạn đang có hơn 100 từ cần ôn tập.
              </p>
              <p className="notify-save-word">
                Ôn tập ngay để lưu thêm từ vào sổ tay
              </p>
              <PrimaryButton
                onClick={() => {
                  setShowModal("");
                  window.open("https://learn.mochidemy.com/user/review?");
                }}
                className="btn_save_notify"
              >
                Ôn tập ngay
              </PrimaryButton>
            </div>
          </Col>
        </Row>
      </MainModal>

      <MainModal
        show={type == "paid-save-under-100"}
        mascot={mochiOver}
        handleClose={() => setShowModal("")}
        className="modal_notify_save_over_TDV"
      >
        <Row className="notify_content">
          <Col md={12}>
            <div>
              <p className="notify-save-word">
                Bạn đã lưu từ thành công và đang có {reviewCount} từ đến giờ ôn
                tập.
              </p>
              <PrimaryButton
                onClick={() => {
                  setShowModal("");
                  window.open("https://learn.mochidemy.com/user/review?");
                }}
                className="btn_save_notify"
              >
                Ôn tập ngay
              </PrimaryButton>
            </div>
          </Col>
        </Row>
      </MainModal>
      <MainModal
        show={type == "free-review"}
        mascot={mochiOver}
        handleClose={() => setShowModal("")}
        className="modal_notify_save_over_TDV"
      >
        <Row className="notify_content">
          <Col md={{ span: 8, offset: 2 }}>
            <div>
              <p className="notify-save-word">
                Bạn đã lưu từ thành công và đang có {reviewCount} từ đến giờ ôn
                tập.
              </p>
              <PrimaryButton
                onClick={() => {
                  setShowModal("");
                  window.open("https://learn.mochidemy.com/user/review?");
                }}
                className="btn_save_notify"
              >
                Ôn tập ngay
              </PrimaryButton>
            </div>
          </Col>
        </Row>
      </MainModal>

      {!isBeforeTDV && (
        <MainModal
          show={type == "normal"}
          mascot={mochiSave}
          handleClose={() => setShowModal("")}
          mascotClass="save_over_TDV"
          className="modal_notify_save_over_TDV"
        >
          <Row className="notify_content">
            <Col md={12}>
              <div>
                <p className="notify-save-word">Bạn đã lưu từ thành công.</p>
                <p className="notify-save-word">
                  Đăng nhập vào website và mở tab SỔ TAY để xem từ vựng vừa lưu
                </p>
                <PrimaryButton
                  onClick={handleOpenWebsite}
                  className="btn_save_notify"
                >
                  Mở website ngay
                </PrimaryButton>
              </div>
            </Col>
          </Row>
        </MainModal>
      )}
      {!expiredDay && (
        <MainModal
          show={type == "overload"}
          mascot={mochiNoti}
          handleClose={() => setShowModal("")}
          className="modal_notify_save"
        >
          <Row className="notify_content">
            <Col md={12}>
              <div>
                <p className="notify-save-word">Bạn đã lưu 30 từ vào sổ tay.</p>
                <p className="notify-save-word">
                  Nâng cấp tài khoản để lưu từ không giới hạn
                </p>
                <PrimaryButton
                  onClick={handleUpgrade}
                  className="btn_save_notify"
                >
                  Nâng cấp ngay
                </PrimaryButton>
              </div>
            </Col>
          </Row>
        </MainModal>
      )}
      {expiredDay && (
        <MainModal
          show={type == "overload"}
          mascot={mochiNoti}
          handleClose={() => setShowModal("")}
          className="modal_notify_save"
        >
          <Row className="notify_content">
            <Col md={12}>
              <div>
                <p className="notify-save-word">Bạn đã lưu 50 từ vào sổ tay.</p>
                <p className="notify-save-word">
                  Nâng cấp tài khoản để lưu từ không giới hạn
                </p>
                <PrimaryButton
                  onClick={handleUpgrade}
                  className="btn_save_notify"
                >
                  Nâng cấp ngay
                </PrimaryButton>
              </div>
            </Col>
          </Row>
        </MainModal>
      )}
    </>
  );
}

export default NotifyModal;
