const ORIGIN = process.env.REACT_APP_ORIGIN;

export const fetchProfile = (userToken) => {
  // console.log = function () {};
  console.log(userToken);
  return (dispatch) => {
    // check dieu kien call Api
    let  timeYesterday = new Date();
    let timeFirstCallApi = timeYesterday.toDateString();
    if (window.localStorage.getItem("timeCallApi") == null) {
      window.localStorage.setItem("timeCallApi", timeFirstCallApi);
    }
    let timeStampFirstApi = new Date(window.localStorage.getItem("timeCallApi")).getTime();

    let timeCurrent = new Date();
    let timeCurrentCallApi = timeCurrent.toDateString();
    let timeCurrentApi = new Date(timeCurrentCallApi).getTime();

    if (userToken && (window.localStorage.getItem("profileNewTab") === null || timeStampFirstApi !== timeCurrentApi) ) {
      dispatch(actions.getAccountInformation(userToken));
      client
        .get("v3.0/profile", { params: { user_token: userToken } })
        .then((response) => {
          const result = response.data;
          console.log({ result });
          if (result.code == "1") {
            window.localStorage.setItem("profileNewTab", JSON.stringify(result.data));
            window.localStorage.setItem("profileToken", JSON.stringify(result.data.user_token));
            chrome.runtime.sendMessage({
              name: "handleProfile",
              dataProfile: result.data,
              dataToken: result.data.user_token
            },function(result) {
              console.log("test", result);
            })
            // chrome.runtime.sendMessage({name:"tabReload"}, function(response){
            // })
            dispatch(actions.fetchProfileCompleted(result.data));
            let timeCallApi = new Date();
            let timeCurrentApiSet = timeCallApi.toDateString();
            window.localStorage.setItem("timeCallApi", timeCurrentApiSet);
          }
          // dispatch(actions.closeLoading());
        })
        .catch((error) => {
          const errorMsg = error.message;
          console.log({ errorMsg });
          // dispatch(actions.fetchListFailed(errorMsg));
        });
    } else {
      let profileData = JSON.parse(localStorage.getItem("profileNewTab"));
      dispatch(actions.getAccountInformation(profileData.user_token));
      // alert("TH khong call API");
      dispatch(actions.fetchProfileCompleted(profileData));
    }
  };
};
export function setBackground(backgroundId) {
  return (dispatch) => {
    if (backgroundId) {
      dispatch(actions.setBackgroundImage(backgroundId));
    };
  }
}
export function sendFeedBack(params, success=() => {}) {
  return (dispatch) => {
    feedbackClient
      .get("", { params: params })
      .then((response) => {
        const result = response.data;
        console.log({ result });
        if (result.code == "success") {
          success(true);
        }
        // dispatch(actions.closeLoading());
      })
      .catch((error) => {
        const errorMsg = error.message;
        console.log({ errorMsg });
        // dispatch(actions.fetchListFailed(errorMsg));
      });
  };
}

