function getCookieLocal(cookieName) {
  const mochiTokenCookie = document.cookie.split(cookieName+"=");
  if (mochiTokenCookie.length === 2) {
    return mochiTokenCookie[1].split(";")[0];
  }
  else {return ''}
}
