export const fetchWordStatistic = (userToken) => {

  return (dispatch) => {
    if (userToken) {
      client
        .get("v3.1/words/summary-ios", {
          params: { user_token: userToken, lang: "en" },
        })
        .then((response) => {
          const result = response.data;
          console.log({ result });
          if (result.code == "1") {
            dispatch(actions.fetchWordStatisticCompleted(result.data));
          }
          // dispatch(actions.closeLoading());
        })
        .catch((error) => {
          const errorMsg = error.message;
          console.log({ errorMsg });
          // dispatch(actions.fetchListFailed(errorMsg));
        });
    }
  };
};

