import * as types from "./types";

export const handleBookMark = ( payload ) => {
    return {
        type: types.GET_BOOKMARK,
        payload: payload
    }
}
export const handleSideBar = ( payload ) => {
    return {
        type: types.GET_SIDEBAR,
        payload: payload
    }
}
export const handleBanner = ( payload ) => {
    return {
        type: types.GET_BANNER,
        payload: payload
    }
}
export const handleInfeed = ( payload ) => {
    return {
        type: types.GET_INFEED,
        payload: payload
    }
}


