/*global chrome*/

import "../component.css";
import MainModal from "../commons/MainModal";
import { Col, Row } from "react-bootstrap";
import { useState } from "react";

import BackgroundContent from "./BackgroundContent";
import QuoteContent from "./QuoteContent";
import ContactContent from "./ContactContent";
import AccountContent from "./AccountContent";

function SettingModal(props) {
  const [selectedSetting, setSelectedSetting] = useState("setting-bg");

  
    const handleSelectSetting = (event) => {
      const selectedId = event.target.id;
      setSelectedSetting(selectedId);
    };
  return (
    <MainModal {...props} size="lg" className="setting-modal-wrapper">
      <div className="setting-modal blur-background">
        <div className="setting-content">
          <BackgroundContent />
        </div>
      </div>
    </MainModal>
  );
}

export default SettingModal;
