import * as types from "./types";

const INITIAL_STATE = {
    listDataBookMark: [],
    listDataSideBar: [],
    listDataBanner: [],
    listDataInFeed: []
};

const reducer = (state = INITIAL_STATE, action) => {
  const payload = action.payload;
  switch (action.type) {
    case types.GET_BOOKMARK:
      return {
        ...state,
        listDataBookMark: payload
      };
    case types.GET_SIDEBAR:
      return {
        ...state,
        listDataSideBar: payload
      };
    case types.GET_BANNER:
      return {
        ...state,
        listDataBanner: payload
      };
    case types.GET_INFEED:
      return {
        ...state,
        listDataInFeed: payload
      };
    default:
        return state;
  }
};
export default reducer;
