const handleIframe = () => {
  
}

function ReviewWord() {
  const accountState = useSelector((state) => state.account);
  const { statistic, totalWord, reviewCount, reviewTime } = accountState;
  const reviewTimeObject = new Date(reviewTime);
  const now = new Date();
  const isShowCountDown = reviewTimeObject.getTime() - now.getTime() > 0;
  // console.log("isShowCountDown");
  const renderBar = (attributes) => {
    let color = "#EB5757";
    switch (attributes.id) {
      case 1:
        color = "#EB5757";
        break;
      case 2:
        color = "#FFCB08";
        break;
      case 3:
        color = "#56CCF2";
        break;
      case 4:
        color = "#2F80ED";
        break;
      case 5:
        color = "#213782";
        break;
      default:
        break;
    }
    let minHeight = attributes.height - 30 < 5 ? 5 : attributes.height - 30;
    minHeight = attributes.height === 0 ? 0 : minHeight;

    let maxY = attributes.y + 20 > 230 ? 230 : attributes.y + 20;
    maxY = attributes.height === 0 ? 235 : maxY;
    const renderComp = (
      <path
        fill={color}
        width="45"
        height={attributes.height}
        id={attributes.id}
        x={attributes.x}
        y={attributes.y}
        radius="0"
        className="recharts-rectangle"
        d={`M ${attributes.x},${maxY} h 45 v ${minHeight} h -45 Z`}
        strokeLinejoin="round"
        stroke={color}
        strokeWidth="10"
      ></path>
    );
    return renderComp;
  };
  const renderLabel = (props) => {
    // console.log("lable", props.height);
    let minY = props.height < 30 ? props.y - 26 : props.y + 7;
    minY = props.height === 30 ? props.y : minY;
    return (
      <>
        <text
          id={props.id}
          x={props.x}
          y={minY}
          width="52"
          height={props.height}
          offset="5"
          className="recharts-text recharts-label"
          textAnchor="middle"
        >
          <tspan x={props.x + 20} dy="0em">
            {props.value ? props.value : 0} từ
          </tspan>
        </text>
        {/* <text
          id={props.id}
          x={props.x}
          y={props.y + props.height + 12}
          width="52"
          height={props.height}
          offset="0"
          className="recharts-text recharts-label"
          textAnchor="middle"
        >
          <tspan x={props.x + 22} dy="0em">
            {props.id}
          </tspan>
        </text> */}
      </>
    );
  };
  const renderer = ({ hours, minutes, seconds }) => (
    <span>
      {zeroPad(hours)}:{zeroPad(minutes)}:{zeroPad(seconds)}
    </span>
  );

  let lineId = useRef();
  useEffect(() => {
    lineId.current = setInterval(() => {
      const xAxis = document.querySelector(".recharts-cartesian-axis-line");
      if (xAxis) {
        const xAxisY1 = xAxis.getAttribute("y1");
        const xAxisY2 = xAxis.getAttribute("y2");
        xAxis.setAttribute("y1", 240);
        xAxis.setAttribute("y2", 240);
        clearInterval(lineId.current);
      }
    }, 100);
  }, []);
  return (
    <div className="review-word-wrapper">
      <div className="word-total-wrapper">
        <div className="word-total-header">
          <img alt="total" src={bookIcon} className="total-word-book-icon" />
          <p className="total-word-title">Bạn đã học được</p>
        </div>
        <h4 className="total-word">{totalWord ? totalWord : 0} từ</h4>
      </div>
      <div className="statistic-wrapper">
        {statistic.length > 0 && (
          <BarChart
            width={350}
            height={280}
            data={statistic}
            margin={{
              top: 5,
              right: 10,
              left: 20,
              bottom: 5,
            }}
          >
            <XAxis dataKey="id" tickLine={false} axisLine={true} />
            <Bar dataKey="total" fill="#EB5757" shape={renderBar}>
              <LabelList dataKey="total" position="top" content={renderLabel} />
            </Bar>
          </BarChart>
        )}
      </div>
      {reviewCount > 0 && (
        <p className="boldText">
          Chuẩn bị ôn tập: <strong>{reviewCount}</strong> từ
        </p>
      )}
      {isShowCountDown ? (
        <OverlayTrigger
          placement="top-start"
          trigger="hover"
          // delay={{ show: 100, hide: 200 }}
          overlay={
            <Tooltip id="countdown-tooltip">
              <div className="countdown-tooltip">
                <p>
                  Vào website ôn tập khi đến Thời Điểm Vàng để tăng 10 lần hiệu
                  quả ghi nhớ
                </p>
              </div>
            </Tooltip>
          }
        >
          <div className="countdown-wrapper">
            <img
              src={hourGlassIcon}
              alt="hourglass"
              className="countdown-hourglass"
            />
            <Countdown
              date={reviewTimeObject}
              daysInHours={false}
              renderer={renderer}
            />
            <img
              src={warningIcon}
              alt="warningIcon"
              className="countdown-warning"
            />
          </div>
        </OverlayTrigger>
      ) : (
        <PrimaryButton
          onClick={()=>{
            window.open("https://learn.mochidemy.com/", "_blank");
          }}
          className="review-now-btn"
          id="review_now"
        >
          Ôn tập ngay
        </PrimaryButton>
      )}
    </div>
  );
}

export default ReviewWord;
