import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import "./index.css";
import InfoHistory from "./InfoHistory";

const HistoryRight = ({ isUrlHistory, handleTracking }) => {
  const bookMarkState = useSelector((state) => state.bookmark);
  const [listDataBookMarkLocal, setListDataBookMarkLocal] = useState([]);
  const { listDataBookMark } = bookMarkState;
  useEffect(() => {
    if (Array.isArray(listDataBookMark) && listDataBookMark.length <= 0 &&   localStorage.getItem("bookMarkState") !== null) {
      let getDataBookMark = JSON.parse(
        localStorage.getItem("bookMarkState")
      ).listDataBookMark;
      setListDataBookMarkLocal(getDataBookMark);
    }
  }, []);

  return (
    <div className="ext__history--left ext__history--border--right">
      {(listDataBookMark.length > 0
        ? listDataBookMark
        : listDataBookMarkLocal
      ).map((item, index) => {
        return (
          <div
            key={index}
            className="ext__history--list--item"
            onClick={() => handleTracking(index, "bookMark")}
          >
            <a href={item.link}>
              <InfoHistory urlImage={item.image} titleHistory={item.text} />
            </a>
          </div>
        );
      })}
    </div>
  );
};

export default HistoryRight;
