/*global chrome*/

import "../component.css";
import mochiCongras from "../../assets/Mochi-meomeo2.png"
import { Col, Row, Spinner } from "react-bootstrap";
import { useState, useEffect } from "react";
import PrimaryButton from "../commons/PrimaryButton";
import MainModal from "../commons/MainModal";
import { useDispatch } from "react-redux";
import {accountOperations} from '../../state/modules/account'

function ContactContent(props) {
  const dispatch = useDispatch();

  const [focus,setFocus] = useState('');
  const [error,setError] = useState("");
  const [email, setEmail] = useState("");
  const [content,setContent] = useState("");
  const [showThankModal,setShowThankModal] = useState(false);
  const [loading, setLoading] = useState(false);
  
  
  
  
  const handleChangeEmail = (event) => {
    const value = event.target.value.trim()
    const regex = /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/g;
    setEmail(value);
    if (value == '') {
      setError("Email không được để trống!");
    }
    else if (!regex.test(value)) {
      setError('Email không đúng định dạng. Vui lòng kiểm tra lại!')
    }
    else {
      
      setError('')
    }
  }
  const handleConfirm = async (e) => {
    const isDisabled = error || !email || !content
    if (!isDisabled) {
      setLoading(true)
      await fetch(
        `https://script.google.com/macros/s/AKfycbwXTpHDuxztM47mOiVKGN36pUg-k7fYeKudCaBHW-fM5FoxSf9uT5QMBN_GDsdkvMaG/exec?email=${email}&comment=${content}`
      )
        .then((response) => response.json())
        .then((data) => {
          if (data.result == 'success') {
            setShowThankModal(true);
            props.setShowBlurBg(true)
            setLoading(false);
            setContent('')
            setEmail("");

          }
        });
    } 
  }

  
  return (
    <>
      <Row>
        <Col md={12}>
          <h4>Góp ý</h4>
          <h6>
            Gửi cho Mochi những góp ý của bạn để Mochi cải thiện hơn nữa nhé
          </h6>
        </Col>
      </Row>
      <Row className="contact-content">
        <div className="contact-wrapper">
          <div className="contact-input">
            <div
              className={`email-input ${
                focus === "email" && error == "" && "outline-background"
              }`}
            >
              <input
                placeholder="Email"
                className={`${error && "error-input"}`}
                onFocus={() => setFocus("email")}
                onBlur={() => setFocus("")}
                onChange={handleChangeEmail}
                value={email}
              />
              {error && (
                <p className="email-error">
                  <small>{error}</small>
                </p>
              )}
            </div>

            <div
              className={`content-input ${
                focus === "content" && "outline-background"
              }`}
            >
              <textarea
                placeholder="Những điều Mochi có thể cải thiện hơn nữa"
                value={content}
                onFocus={() => setFocus("content")}
                onBlur={() => setFocus("")}
                onChange={(e) => setContent(e.target.value)}
              />
            </div>
            <div style={{ display: "flex", alignItems: "center" }}>
              <PrimaryButton
                disabled={error || !email || !content || loading}
                id="contact-confirm"
                className="contact-confirm"
                onClick={handleConfirm}
              >
                Gửi
              </PrimaryButton>
              {loading && (
                <Spinner
                  animation="border"
                  variant="warning"
                  style={{ marginLeft: 10 }}
                />
              )}
            </div>
          </div>
        </div>
      </Row>
      {showThankModal && <div className="modal-background"></div>}
      <MainModal
        mascot={mochiCongras}
        show={showThankModal}
        handleClose={() => {
          setShowThankModal(false);
          props.setShowBlurBg(false);
          setContent("");
          setEmail("");
          setError("");
        }}
        className="thank-you-modal"
      >
        <div className="forget-notice-wrapper">
          <p className="boldText">
            Cảm ơn bạn iu đã góp ý cho Mochi nhé {"<3"}
          </p>
        </div>
      </MainModal>
    </>
  );
}

export default ContactContent;
