/*global chrome*/

import "../component.css";
import MainModal from "../commons/MainModal";

function EventModal(props) {
  const imageLocal = JSON.parse(localStorage.getItem("dataEvent"));
  // console.log("imageLocal", imageLocal.image);
  return (
    <MainModal {...props} size="lg" className="setting-modal-wrapper">
      <a href={imageLocal[0].link} target="_blank">
        <img
        src = {imageLocal[0].image}
        alt = "event"
        className="event-modal"
        />
      </a>
    </MainModal>
  );
}

export default EventModal;
