import React from 'react'
import ellipse from "../../assets/Ellipse.png";

function NumberNotice() {
 
  let resultNumber = localStorage.getItem('dataEvent');
  let numberArray = JSON.parse(resultNumber);

  return (
    <div className="number__circle--notice">
        { resultNumber !== null && numberArray.length > 0 ? 
          <img
              src={ellipse}
              alt="ellipse"
              className="ellipse-icon"
          /> : ""
        }
        <p className="text__number">{numberArray && numberArray.length > 0 ? numberArray.length :""}</p>
    </div>
  )
}

export default NumberNotice