const ROOT_LINK = process.env.REACT_APP_ROOT_LINK; 
const PRIVATE_KEY = process.env.REACT_APP_PRIVATE_KEY;
const getClient = (baseUrl = null) => {
const options = {
  baseURL: baseUrl ? baseUrl : ROOT_LINK + "/",
};
//  console.log(apiOptions);
 options.headers = {
   privateKey: "M0ch1M0ch1_En_$ecret_k3y",
   deviceType: "3",
   appVersion: "2.3",
   "Content-Type": "application/json",
 };
  const client = axios.create(options);
  
    // Add a response interceptor
    client.interceptors.response.use(
      (response) => response,
      (error) => {
        if (error.response.status >= 500) {
          Raven.captureException(error);
        }
        return Promise.reject(error);
      }
  );
  return client;
};
class ApiClient {
  constructor(baseUrl = null) {
    this.client = getClient(baseUrl);
  }
  
  get(endpoint, conf = {}) {
    return this.client
      .get(endpoint, conf)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error));
  }
  delete(endpoint, conf = {}) {
    return this.client
      .delete(endpoint, conf)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error));
  }
  head(endpoint, conf = {}) {
    return this.client
      .head(endpoint, conf)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error));
  }
  options(endpoint, conf = {}) {
    return this.client
      .options(endpoint, conf)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error));
  }
  post(endpoint, data = {}, conf = {}) {
    return this.client
      .post(endpoint, data, conf)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error));
  }
  put(endpoint, data = {}, conf = {}) {
    return this.client
      .put(endpoint, data, conf)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error));
  }
  patch(endpoint, data = {}, conf = {}) {
    return this.client
      .patch(endpoint, data, conf)
      .then((response) => Promise.resolve(response))
      .catch((error) => Promise.reject(error));
  }
}
export { ApiClient };
