/*global chrome*/

import "./index.css";

import mochiIcon from "../../assets/logo_orange.png";
import exitIcon from "../../assets/button_exit.png";
import PrimaryButton from "../commons/PrimaryButton";
import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import EVResult from "./EVResult";
import EEResult from "./EEResult";
import {Row, Col} from 'react-bootstrap'
const ORIGIN = process.env.REACT_APP_ORIGIN;

function ResultModal({ setShowResult, handleSearchSuggest }) {
  const [selectTab, setSelectTab] = useState(0);
  const [showMore, setShowMore] = useState(false);

  const wordState = useSelector((state) => state.word);

  const { usWord, vnWord, suggest, wordToSearch } = wordState;
  // console.log("wordState", wordState);
  const handleChangeTab = (next) => {
    setSelectTab(next);
    setShowMore(false);
  };
  useEffect(() => {
    let dictCookie = "";
    
      // console.log({ getCookies });
      // getCookies(ORIGIN, "defaultDict", function (cookie) {
      //   dictCookie = cookie === 'en-en' ? 1 : 0;
      //   console.log({ dictCookie });
      //   if (dictCookie) {
      //     setSelectTab(dictCookie);
      //   }
        // setDisableNewTab(mochiToken);
      // });
      chrome.runtime.sendMessage("getLanguage",function (response){
          if(response === "1"){
            setSelectTab(0);
          } else if(response === "2"){
            setSelectTab(1);
          } else {
            setSelectTab(0);
          }
      })
    
  }, []);
function getCookies(domain, name, callback) {
  chrome.cookies.get({ url: domain, name: name }, function (cookie) {
    if (callback && cookie) {
      callback(cookie.value);
    }
  });
}
console.log({ suggest });
  return (
    <div className="search-result-wrapper">
      <img
        src={exitIcon}
        alt="exit icon"
        className="exit-icon"
        onClick={() => setShowResult(false)}
      />

      <div className="dict-select-wrapper">
        <div
          className={`dict-tab ${selectTab === 0 && "selected"}`}
          onClick={() => handleChangeTab(0)}
        >
          Anh - Việt
        </div>
        <div
          className={`dict-tab ${selectTab === 1 && "selected"}`}
          onClick={() => handleChangeTab(1)}
        >
          Anh - Anh
        </div>
      </div>
      <div className="result-main">
        {selectTab == 0 && vnWord.length > 0 && (
          <EVResult
            vnWord={vnWord}
            showMore={showMore}
            handleReSearch={(value) => handleSearchSuggest(value)}
          />
        )}
        {selectTab == 1 && usWord.length > 0 && (
          <EEResult usWord={usWord} showMore={showMore} />
        )}
        {vnWord.length == 0 && usWord.length == 0 && suggest.length > 0 && (
          <div className="suggest-wrapper">
            {selectTab == 0 && (
              <p>
                <i>Có phải bạn đang tìm từ này?</i>
              </p>
            )}
            {selectTab == 1 && (
              <p>
                <i>Did you mean:</i>
              </p>
            )}

            {suggest.map((item, index) => (
              <p
                key={index}
                className="suggest-word"
                onClick={() => handleSearchSuggest(item)}
              >
                {item}
              </p>
            ))}
          </div>
        )}
        {vnWord.length === 0 && usWord.length === 0 && suggest.length === 0 && (
          <div className="not-found-wrapper">
            {selectTab == 0 && (
              <p>
                <i>
                  Mochi không tìm thấy kết quả cho từ này. Bạn thử tra cứu từ
                  khác xem nhé ^^
                </i>
              </p>
            )}
            {selectTab == 1 && (
              <p>
                <i>There is no result for this word. Try another word!</i>
              </p>
            )}
          </div>
        )}
      </div>
      <Row className="result-footer">
        <Col md={7} className="show-more-btn">
          {selectTab == 0 &&
            (vnWord.length > 1 ||
              (vnWord[0] && vnWord[0].detail.length > 1)) && (
              <small onClick={() => setShowMore(!showMore)}>
                {!showMore ? "Xem thêm các nghĩa khác" : "Rút gọn"}
              </small>
            )}
          {selectTab == 1 &&
            (usWord.length > 1 ||
              (usWord[0] && usWord[0].detail.length > 1)) && (
              <small onClick={() => setShowMore(!showMore)}>
                {!showMore ? "See more" : "Hide"}
              </small>
            )}
        </Col>
        <Col md={{ span: 4, offset: 1 }}>
          <img src={mochiIcon} alt="mochi icon" className="mochi-icon" />
        </Col>
      </Row>
    </div>
  );
}

export default ResultModal;
