import * as types from './types'

export const searchWord = (payload, key) => {
  return {
    type: types.SEARCH_WORD,
    payload: payload,
    key,
  };
};
export const firstSearch = (payload, key) => {
  return {
    type: types.FIRST_SEARCH,
    payload: payload,
    key,
  };
};
export const searchWordParagraph = (payload) => {
  return {
    type: types.SEARCH_PARAGRAPH,
    payload: payload,
  };
};
export const setLoading = (payload) => {
  return {
    type: types.SET_LOADING,
    payload: payload,
  };
};
