/*global chrome*/

import "../component.css";
import { Col, Row } from "react-bootstrap";
import { useState, useEffect } from "react";
import mascotGuide from "../../assets/Mochi-chi-dan.png";
import background0 from "../../assets/images/bg_extension_0.jpg";
import background1 from "../../assets/images/bg_extension_1.jpg";
import background2 from "../../assets/images/bg_extension_2.jpg";
import background3 from "../../assets/images/bg_extension_3.jpg";
import background4 from "../../assets/images/bg_extension_4.jpg";
import background5 from "../../assets/images/bg_extension_5.jpg";
import background6 from "../../assets/images/bg_extension_6.png";
import background7 from "../../assets/images/bg_extension_7.png";
import background8 from "../../assets/images/bg_extension_8.png";
import background9 from "../../assets/images/bg_extension_9.png";
import background10 from "../../assets/images/bg_extension_10.png";
import check from "../../assets/check.png";
import LoginModal from "./LoginModal";
import { useSelector } from "react-redux";

function BackgroundContent(props) {
  chrome.runtime.sendMessage("getBackground", function (response) {
    if (response) {
      window.localStorage.setItem("selectedBg", response);
      setSelectedImg(response);
    }
  });
  const selectedBg = window.localStorage.getItem("selectedBg")
    ? window.localStorage.getItem("selectedBg")
    : "./static/media/bg_extension_0.b80c3bdb182ced845919";

  const [showLoginModal, setShowLoginModal] = useState(false);

  const [selectedImg, setSelectedImg] = useState(selectedBg);
  const accountState = useSelector((state) => state.account);
  const { userToken } = accountState;
  const listBackground = [
    {
      name: background7,
      index: 7,
    },
    {
      name: background8,
      index: 8,
    },
    {
      name: background9,
      index: 9,
    },
    {
      name: background10,
      index: 10,
    },
    {
      name: background0,
      index: 0,
    },
    {
      name: background6,
      index: 6,
    },
    {
      name: background1,
      index: 1,
    },
    {
      name: background2,
      index: 2,
    },
    {
      name: background3,
      index: 3,
    },
    {
      name: background4,
      index: 4,
    },
    {
      name: background5,
      index: 5,
    },
  ];
  const handleSelectImg = (name) => {
    if (userToken) {
      setSelectedImg(name);
      window.localStorage.setItem("selectedBg", name);
      chrome.runtime.sendMessage(
        {
          name: "saveBackground",
          data: name,
        },
        function (response) {}
      );
      chrome.runtime.sendMessage("getBackground", function (response) {
        const container = document.getElementById("mochi-new-tab_main");
        container.style.backgroundImage = `url(${response})`;
      });

      chrome.runtime.sendMessage("reloadBgc");
    } else {
      setShowLoginModal(true);
      props.setShowBlurBg(true);
    }
  };
  chrome.runtime.sendMessage("bgcNewTab", function (response) {
    if (response === true) {
      localStorage.setItem("disableNewTab", "0");
    } else if (response === false) {
      localStorage.setItem("disableNewTab", "1");
    } else {
      localStorage.setItem("disableNewTab", "0");
    }
  });
  chrome.runtime.sendMessage("getStatusNewFeed", function (response) {
    if (response === "1") {
      window.localStorage.setItem("newfeed", "1");
      setSelectNewFeed(true);
    } else if (response === "0") {
      window.localStorage.setItem("newfeed", "0");
      setSelectNewFeed(false);
    } else {
      window.localStorage.setItem("newfeed", "1");
      setSelectNewFeed(true);
    }
  });
  const newTabStatus =
    localStorage.getItem("disableNewTab") == "1" ? false : true;
  const defaultNewTab = localStorage.getItem("defaultDict")
    ? localStorage.getItem("defaultDict")
    : "en-vn";
  const newStatusNewFeed =
    localStorage.getItem("newfeed") == "1" ? true : false;

  const [selectDefaultNewTab, setSelectDefaultNewTab] = useState(() => {
    chrome.runtime.sendMessage("getLanguage", function (response) {
      if (response === "1") {
        setSelectDefaultNewTab(1);
      } else if (response === "2") {
        setSelectDefaultNewTab(2);
      } else {
        setSelectDefaultNewTab(1);
      }
    });
  });
  const [selectNewTab, setSelectNewTab] = useState(newTabStatus);
  const [selectNewFeed, setSelectNewFeed] = useState(
    newStatusNewFeed ? newStatusNewFeed : true
  );
  // const [changeNewTab,setChangeNewTab] = useState();
  const handleChangeDict = (e) => {
    const value = e.target.value;
    chrome.runtime.sendMessage({
      name: "changeLanguage",
      data: value,
    });
    chrome.runtime.sendMessage("reloadBgc");
    setSelectDefaultNewTab(value);
    localStorage.setItem("defaultDict", value);
  };
  const handleChangeNewTab = () => {
    const newStatus = !selectNewTab;
    localStorage.setItem("disableNewTab", newStatus ? 0 : 1);
    setSelectNewTab(!selectNewTab);
    if (localStorage.getItem("disableNewTab") === "1") {
      chrome.runtime.sendMessage("reloadNewTabAll");
      chrome.runtime.sendMessage("OffNewTab");
      chrome.runtime.sendMessage("newTabOff");
    } else {
      chrome.runtime.sendMessage("reloadNewTabAll");
      chrome.runtime.sendMessage("newTabOn");
    }
    window.location.reload(false);
  };

  const handleChangeNewFeed = () => {
    localStorage.setItem("newfeed", !selectNewFeed ? 1 : 0);
    setSelectNewFeed(!selectNewFeed);
    if (localStorage.getItem("newfeed") === "1") {
      chrome.runtime.sendMessage({
        name: "newFeedOn",
      });
    } else if (localStorage.getItem("newfeed") === "0") {
      chrome.runtime.sendMessage({
        name: "newFeedOff",
      });
    }
    window.location.reload(false);
    chrome.runtime.sendMessage("reloadAllPage");
  };
  return (
    <>
      <Row>
        <Col md={12}>
          <h4>Cài đặt chung</h4>
        </Col>
      </Row>
      {/* Chọn từ điển */}
      <Row className="background-content">
        <Col lg={12}>
          <div className="info-setting-wrapper">
            <div
              className="setting-dictionary"
              style={{
                width: "94%",
              }}
            >
              <p className="dict-title boldText">Chọn từ điển mặc định</p>
              <select
                className="dict-select boldText"
                onChange={handleChangeDict}
                value={selectDefaultNewTab}
              >
                <option value="1" className="dict-option boldText">
                  Từ điển Anh - Việt
                </option>
                <option value="2" className="dict-option boldText">
                  Từ điển Anh - Anh
                </option>
              </select>
            </div>
          </div>
        </Col>
        <Col lg={12}>
          <div
            className="setting-new-tab"
            style={{
              width: "93%",
            }}
          >
            <p className="new-tab-title boldText">Giao diện Mochi</p>
            <label className="switch">
              <div className="toggle">
                <input
                  className="toggle-state"
                  type="checkbox"
                  name="check"
                  check="check"
                  checked={selectNewTab}
                  onChange={handleChangeNewTab}
                />
                <div className="toggle-inner">
                  <div className="indicator"></div>
                </div>
                <div className="active-bg"></div>
              </div>
              <p>{selectNewTab ? "On" : "Off"}</p>
            </label>
          </div>
        </Col>
        {/* Facebook */}
        <Col lg={12}>
          <div
            className="setting-new-tab"
            style={{
              width: "93%",
            }}
          >
            <p className="new-tab-title boldText">
              Chức năng khóa Bảng tin Facebook
            </p>
            <label className="switch">
              <div className="toggle">
                <input
                  className="toggle-state"
                  type="checkbox"
                  name="check"
                  check="check"
                  checked={selectNewFeed}
                  onChange={handleChangeNewFeed}
                />
                <div className="toggle-inner">
                  <div className="indicator"></div>
                </div>
                <div className="active-bg"></div>
              </div>
              <p>{selectNewFeed ? "On" : "Off"}</p>
            </label>
          </div>
        </Col>
        {listBackground.map((item) => {
          return (
            <Col md={6} key={item.index}>
              <div className="image-item-wrapper">
                  <img
                    src={item.name}
                    alt={"background" + item.index}
                    className="image-item"
                    onClick={() => {
                      handleSelectImg(item.name);
                    }}
                  />
                {selectedImg == item.name && (
                  <img src={check} alt={"check-btn"} className="check-btn" />
                )}
              </div>
            </Col>
          );
        })}
        <LoginModal
          show={showLoginModal}
          mascot={mascotGuide}
          handleClose={() => {
            setShowLoginModal(false);
            props.setShowBlurBg(false);
          }}
          className="login-modal-in-modal"
        ></LoginModal>
      </Row>
    </>
  );
}

export default BackgroundContent;
