import React from "react";
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import "./index.css";
import iconFacebook from "../../../assets/Facebook-icon.png";
const InfoHistory = ({ urlImage, titleHistory }) => {
  return (
    <OverlayTrigger
      placement="bottom"
      overlay={titleHistory ? <Tooltip>{titleHistory}</Tooltip> : <></>}
    >
      <div className="ext__history--mochi">
        <div className="ext__history--item">
          <img
            src={urlImage ? urlImage : iconFacebook}
            alt="icon Mochi"
            className="ext__history--image"
          />
        </div>
        <div className="ext__history--name">
          <span>{titleHistory ? titleHistory : "Mochi mochi"}</span>
        </div>
      </div>
    </OverlayTrigger>
  );
};

export default InfoHistory;
