import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { useRef } from "react";
import "./index.css";

const ListVideo = ({ urlImage, urlText, link, index, handleTracking }) => {
  const [isHeight, setHeight] = useState();
  const imageDom = useRef();

  useEffect(() => {
    console.log(imageDom.current.offsetWidth);
    let getWidth = imageDom.current.offsetWidth;
    if (Number(getWidth)) {
      let ratio = getWidth / 16;
      let height = ratio * 9;
      console.log("========height=========", height);
      setHeight(height);
    }
  });

  return (
    <a href={link} className="ext__mochi--link--click">
      <div
        className="ext__mochi--listvideo"
        key={index}
        onClick={() => handleTracking(index, "infeed")}
      >
        {/* <LazyLoad> */}
        <img
          src={urlImage}
          alt=""
          className="ext__mochi--video--image"
          ref={imageDom}
          height={isHeight + `px`}
        />
        {/* </LazyLoad> */}
        <div className="ext__mochi--video--title">
          <p>{urlText}</p>
        </div>
      </div>
    </a>
  );
};

export default ListVideo;
