/*global chrome*/
import { useEffect, useState } from "react";
import { Container } from "react-bootstrap";
import { app } from "./firebase-config";
import "bootstrap/dist/css/bootstrap.min.css";
import background0 from "./assets/images/bg_extension_0.jpg";
import "./App.css";
import FirstRow from "./components/FisrtRow/FirstRow";
import SecondRow from "./components/SecondRow/SecondRow";
import { accountOperations } from "./state/modules/account/";
import { wordOperations } from "./state/modules/word/";
import { bookMarkOperations } from "./state/modules/bookmark/";
// import env from 'dotenv'
import { useSelector, useDispatch } from "react-redux";
import LeftNewTab from "./components/LeftNewTab/LeftNewTab";
import Version from "./components/commons/Version";
// import { API_OPTIONS as apiOptions } from "../constant/constant";
const ROOT_LINK = process.env.REACT_APP_ROOT_LINK;

function App() {
  // console.log("%c Mochi Mochi ^^", "text-align: center;font-size: 50px;color: yellow;letter-spacing: .05em;text-shadow: 4px 4px 0px #d5d5d5, 7px 7px 0px rgba(0, 0, 0, 0.2);height: 100px;padding-top: 40px;");
  console.log = function () {};
  const [statusTab, setStatusTab] = useState(false);
  const dataImageLocal = localStorage.getItem("selectedBg");

  const ORIGIN = process.env.REACT_APP_ORIGIN;
  const [userToken, setUserToken] = useState("");
  const [disableNewTab, setDisableNewTab] = useState(false);
  const [isDataTopBar, setDataTopBar] = useState([]);

  const queryParams = new URLSearchParams(window.location.search);
  const page = queryParams.get("p");
  const accountState = useSelector((state) => state.account);
  const bookMarkState = useSelector((state) => state.bookmark);
  const { listDataBookMark } = bookMarkState;
  // Luu du lieu lay ve  len localStorage
  if (listDataBookMark.length > 0) {
    localStorage.setItem("bookMarkState", JSON.stringify(bookMarkState));
  }

  const dispatch = useDispatch();
  const { backgroundId } = accountState;
  // dispatch(accountOperations.fetchProfile());
  // console.log({ ORIGIN });
  useEffect(() => {
    chrome.runtime.sendMessage("tracking-new_tab");

    // Check thời gian hiện popup lưu từ trong ngày.
    let clickSaveCurrent = new Date().toDateString();
    let timeStampClick = new Date(clickSaveCurrent).getTime();
    chrome.runtime.sendMessage("getTimeFirstSaveDay", function (response) {
      let currentTime = response;
      // console.log(currentTime)
      let currentTimeLocal = new Date(currentTime);
      let timeStampSearch = currentTimeLocal.getTime();
      if (timeStampSearch !== timeStampClick) {
        chrome.runtime.sendMessage({
          name: "saveFirstSaveDay",
          data: clickSaveCurrent,
        });
        chrome.storage.sync.set({ firstTimeSave: false }, function () {});
      }
    });

    chrome.runtime.sendMessage("getBackground", function (response) {
      if (response === null) {
        const container = document.getElementById("mochi-new-tab_main");
        container.style.backgroundImage = `url(./static/media/bg_extension_0.b80c3bdb182ced845919.jpg)`;
      } else {
        const container = document.getElementById("mochi-new-tab_main");
        container.style.backgroundImage = `url(${response})`;
      }
    });
    chrome.runtime.sendMessage("bgcNewTab", function (response) {
      if (response === false) {
        chrome.tabs.update({
          url: "chrome-search://local-ntp/local-ntp.html",
        });
        // window.open("https://mochidemy.com/extension-page-login/?disableNT=1");
      } else {
      }
    });
  }, []);

  // },[]);
  // Lưu trên local

  useEffect(() => {
    let userTokenCookie = "";
    if (ORIGIN == "localhost") {
      userTokenCookie = getCookieLocal("mochiToken");
      setUserToken(userTokenCookie);
      if (userTokenCookie) {
        dispatch(accountOperations.fetchProfile(userTokenCookie));
        dispatch(accountOperations.fetchWordStatistic(userTokenCookie));
      } else {
        window.localStorage.removeItem("firstTimeSave");
        window.localStorage.removeItem("profileToken");
      }
    } else {
      // console.log({ getCookies });
      getCookies(ORIGIN, "mochiToken", function (cookie) {
        window.localStorage.setItem("reload", "1");
        userTokenCookie = cookie;
        // console.log("cookie", { userTokenCookie });
        if (userTokenCookie) {
          dispatch(accountOperations.fetchProfile(userTokenCookie));
          dispatch(accountOperations.fetchWordStatistic(userTokenCookie));
        } else {
          window.localStorage.removeItem("firstTimeSave");
          window.localStorage.removeItem("profileToken");
        }
        // setDisableNewTab(mochiToken);
      });
    }
  }, []);
  chrome.runtime.onMessage.addListener(function (
    request,
    sender,
    sendResponse
  ) {
    if (request.fileData === "handle_reload") {
      // window.location.reload();
      if (window.localStorage.getItem("reload") === "1") {
        window.localStorage.removeItem("reload");
        window.location.reload();
      }
    }
  });
  chrome.runtime.onMessage.addListener(function (
    request,
    sender,
    sendResponse
  ) {
    if (request.fileData == "handle_not_login") {
      window.localStorage.removeItem("profileToken");
      window.localStorage.removeItem("profileNewTab");
      if (window.localStorage.getItem("reload") === "1") {
        window.localStorage.removeItem("reload");
        window.location.reload();
      }
    }
  });

  function getCookies(domain, name, callback) {
    chrome.cookies.get({ url: domain, name: name }, function (cookie) {
      if (callback && cookie) {
        callback(cookie.value);
      } else if (cookie === null) {
        if (window.localStorage.getItem("reload") === "1") {
          window.localStorage.removeItem("reload");
          window.location.reload();
        }
        window.localStorage.removeItem("profileNewTab");
        window.localStorage.removeItem("profileToken");
      }
    });
  }
  function getCookieLocal(cookieName) {
    const mochiTokenCookie = document.cookie.split(cookieName + "=");
    if (mochiTokenCookie.length === 2) {
      return mochiTokenCookie[1].split(";")[0];
    } else {
      window.localStorage.removeItem("profileNewTab");
      window.localStorage.removeItem("profileToken");
      return "";
    }
  }
  // Check data Event
  useEffect(() => {
    const options = {
      method: "POST",
      headers: {
        privateKey: "M0ch1M0ch1_En_$ecret_k3y",
        deviceType: "3",
        appVersion: "2.3",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ device: "Extension" }),
    };
    // Check thời gian callApi lấy sự kiện trong ngày.
    let currentTime = new Date().toDateString();
    let timeStampEvent = new Date(currentTime).getTime();
    let timeCallApi = window.localStorage.getItem("timeCallApi");
    let timeStampEventLocal = new Date(timeCallApi).getTime();
    if (timeStampEventLocal !== timeStampEvent) {
      fetch(`${ROOT_LINK}/v3.1/mochi-event`, options)
        .then((res) => res.json())
        .then((data) => {
          // console.log("data", data.data);
          let dataEventLocal = JSON.stringify(data.data);
          localStorage.setItem("dataEvent", dataEventLocal);
        })
        .catch((error) => {
          const errorMsg = error.message;
          // console.log({ errorMsg });
        });
    }
  }, []);

  const selectedBg = dataImageLocal ? dataImageLocal : background0;

  const getDataTopBar = (data) => {
    setDataTopBar(data);
  };
  const handleTrackingSideBar = (index) => {
    chrome.runtime.sendMessage(`sideBar${index}`);
  };

  useEffect(() => {
    // Check thoi gian thoa man dieu kien 2 ngay khac nhau.
    let currentDate = new Date().toDateString();
    let timeStampCurrent = new Date(currentDate).getTime();
    let localTime = localStorage.getItem("timeApiNewTab");
    let timeStampLocal = new Date(localTime).getTime();
    // Lay ra gio hien tai
    let getCurrentHour = new Date().getHours();
    let getCurrentMinute = new Date().getMinutes();
    console.log("=======hour======", getCurrentHour, getCurrentMinute);
    let checkFirst = localStorage.getItem("isFirst");
    if (localTime === null && checkFirst === null) {
      dispatch(
        bookMarkOperations.handleGetBookMark("", (result) => {
          console.log({ result });
        })
      );
      localStorage.setItem("isFirst", true);
    } else if (
      timeStampCurrent !== timeStampLocal &&
      (getCurrentHour > 9 || (getCurrentHour === 9 && getCurrentMinute > 30))
    ) {
      dispatch(
        bookMarkOperations.handleGetBookMark("", (result) => {
          console.log({ result });
        })
      );
      localStorage.setItem("timeApiNewTab", currentDate);
    }
  }, []);

  return (
    <>
      {/* {!page && ( */}
      <div
        className="mochi-new-tab_main"
        id="mochi-new-tab_main"
        style={{
          backgroundImage: `url(${selectedBg})`,
          display: "flex",
        }}
      >
        <LeftNewTab
          isDataTopBar={isDataTopBar}
          handleTrackingSideBar={handleTrackingSideBar}
        />
        <Container className="mochi-new-tab_container">
          <FirstRow getDataTopBar={getDataTopBar} />
          <SecondRow />
        </Container>
        <Version />
      </div>
      {/* )}
      {page === "info" && <Information />}
      {page === "login" && <Login />}
      {page === "register" && <Register />} */}
    </>
  );
}

export default App;
