/*global chrome*/

import "./index.css";
import { v4 as uuidv4 } from "uuid";
import speakerBR from "../../assets/speaker-br.png";
import speakerNA from "../../assets/speaker-na.png";
import plusIcon from "../../assets/plus-icon.png";
import savedWordIcon from "../../assets/saved-word.png";
import mochiNoti from "../../assets/Mochi-thong-bao.png";
import mochiTDV from "../../assets/Mochi-meomeo2.png";
import speakerBRGif from "../../assets/speaker-gif-blue.gif";
import speakerNAGif from "../../assets/speaker-gif-red.gif";
import PrimaryButton from "../commons/PrimaryButton";
import { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { wordOperations } from "../../state/modules/word";
import { Col, Row } from "react-bootstrap";
import MainModal from "../commons/MainModal";
import NotifyModal from "./NotifyModal";
import SecondaryButton from "../commons/SecondaryButton";
import { useEffect } from "react";

function EVResult({ vnWord, showMore, handleReSearch }) {
  console.log("vnwork", vnWord[0]);
  const [isPlaying, setIsPlaying] = useState("");
  const [showModal, setShowModal] = useState("");
  const [wordSaved, setWordSaved] = useState("");
  const [disableSave, setDisableSave] = useState("");

  chrome.storage.sync.get(["gaTracking"], function (result) {
    if (result.gaTracking !== undefined) {
      setMyuuid(result.gaTracking);
    } else if (result.gaTracking === undefined) {
      let newuuid = uuidv4();
      setMyuuid(newuuid);
      chrome.storage.sync.set({ gaTracking: newuuid }, function () {});
    }
  });
  const [myuuid, setMyuuid] = useState("");

  const accountState = useSelector((state) => state.account);

  useEffect(() => {
    chrome.runtime.sendMessage("tracking-new-tab");
  }, []);

  const {
    userToken,
    totalWord,
    reviewCount,
    reviewTime,
    expiredDay,
    statistic,
  } = accountState;

  const dispatch = useDispatch();

  const handlePlaySound = (type, sound, index) => {
    // console.log = function () {};
    if (index) {
      setIsPlaying(type + index);
    } else {
      setIsPlaying(type);
    }
    sound.play();
    console.log("play");
    setTimeout(() => {
      setIsPlaying("");
      console.log("stop");
      // sound.pause();
    }, 1000);
  };

  const handleSaveWord = (wordId, word) => {
    chrome.runtime.sendMessage("save_word_button");
    console.log({ wordId });
    let firstTimeSave = false;
    chrome.storage.sync.get(["firstTimeSave"], function (result) {
      firstTimeSave =
        typeof result.firstTimeSave !== "undefined"
          ? result.firstTimeSave
          : false;
    });
    console.log({ firstTimeSave });
    // setShowModalTDV(true)
    const data = new FormData();
    data.append("word_id", wordId);
    console.log("token", userToken);
    data.append("user_token", userToken);
    data.append("uuid", myuuid);
    const typeModal = checkConditionShowModal();
    console.log({ typeModal });
    if (typeModal === "save") {
      dispatch(
        wordOperations.saveWord(data, (result) => {
          console.log("result", result);
          console.log("result.code", result.code);
          if (result.code == "1") {
            if (result.first_save === true) {
              chrome.runtime.sendMessage("showPopupSave");
            }
            if (!firstTimeSave) {
              setShowModal("normal");
              chrome.storage.sync.set({ firstTimeSave: true }, function () {});
            }
            setWordSaved(wordId);
            setDisableSave(wordId);
            handleReSearch(word);
          }
          if (result.code == "2") {
            switch (result.case) {
              case 1:
                setShowModal("free-over-50lv1");
                break;
              case 2:
                if (result.first_save === true) {
                  chrome.runtime.sendMessage("showPopupSave");
                }
                setShowModal("overload");
                break;
              case 3:
                if (expiredDay && reviewCount >= 100) {
                  setShowModal("paid-over-100");
                } else if (expiredDay && statistic[0].total >= 100) {
                  setShowModal("paid-over-100-lv1");
                }
                break;
              default:
                setShowModal("overload");
                break;
            }
          }
        })
      );
    } else if (
      typeModal === "paid-save-under-100" ||
      typeModal === "free-review"
    ) {
      dispatch(
        wordOperations.saveWord(data, (result) => {
          console.log("result.code", result.code);
          if (result.code == "1") {
            if (result.first_save === true) {
              chrome.runtime.sendMessage("showPopupSave");
            }
            if (!firstTimeSave) {
              setShowModal(typeModal);
              // window.localStorage.setItem("firstTimeSave", true);
              chrome.storage.sync.set({ firstTimeSave: true }, function () {});
            }
            setWordSaved(wordId);
            setDisableSave(wordId);
            handleReSearch(word);
          }
          if (result.code == "2") {
            switch (result.case) {
              case 1:
                setShowModal("free-over-50lv1");
                break;
              case 2:
                setShowModal("overload");
                chrome.runtime.sendMessage("modalPopup30");
                if (result.first_save === true) {
                  chrome.runtime.sendMessage("showPopupSave");
                }
                break;
              case 3:
                if (expiredDay && reviewCount >= 100) {
                  setShowModal("paid-over-100");
                } else if (expiredDay && statistic[0].total >= 100) {
                  setShowModal("paid-over-100-lv1");
                }
                break;
              default:
                setShowModal("overload");
                break;
            }
          }
        })
      );
    } else {
      setShowModal(typeModal);
    }
  };
  const checkConditionShowModal = () => {
    // if (!expiredDay && statistic[0].total >= 50) {
    //   return "free-over-50lv1";
    // }
    // else
    if (!expiredDay && reviewCount > 0) {
      return "free-review";
      // } else if (expiredDay && statistic[0].total >= 100) {
      //   return "paid-over-100-lv1";
      // } else if (expiredDay && reviewCount >= 100) {
      //   return "paid-over-100";
    } else if (expiredDay && statistic[0].total < 100 && reviewCount < 100) {
      return "paid-save-under-100";
    } else {
      return "save";
    }
  };

  const ukSound = vnWord[0].audio_uk && new Audio(vnWord[0].audio_uk);
  const usSound = vnWord[0].audio_us && new Audio(vnWord[0].audio_us);
  return (
    <>
      <div className="result-wrapper en-vi-result">
        <div className="result-field-wrapper">
          {showMore &&
            vnWord.map((item, index) => {
              const ukSoundItem = item.audio_uk && new Audio(item.audio_uk);
              const usSoundItem = item.audio_us && new Audio(item.audio_us);
              return (
                <div key={index}>
                  <h5>{item.content}</h5>
                  <div className="pronounce-field">
                    <Row>
                      <Col md={2}>
                        <p className="br-title">BrE</p>
                      </Col>
                      <Col md={2}>
                        {ukSoundItem && (
                          <img
                            src={
                              isPlaying == "uk" + index
                                ? speakerBRGif
                                : speakerBR
                            }
                            alt="speaker"
                            className="speaker-icon"
                            onClick={() =>
                              handlePlaySound("uk", ukSoundItem, index)
                            }
                          />
                        )}
                      </Col>
                      <Col md={8}>
                        <p className="br-pronounce">{item.phonetic_uk}</p>
                      </Col>
                    </Row>
                  </div>
                  <div className="pronounce-field">
                    <Row>
                      <Col md={2}>
                        <p className="na-title">NAmE</p>
                      </Col>
                      <Col md={2}>
                        {usSoundItem && (
                          <img
                            src={
                              isPlaying == "us" + index
                                ? speakerNAGif
                                : speakerNA
                            }
                            alt="speaker"
                            className="speaker-icon"
                            onClick={() =>
                              handlePlaySound("us", usSoundItem, index)
                            }
                          />
                        )}
                      </Col>
                      <Col md={8}>
                        <p className="br-pronounce">{item.phonetic_us}</p>
                      </Col>
                    </Row>
                  </div>
                  {item.detail.length > 0 ? (
                    item.detail.map((detail) => {
                      return (
                        <div className="result-field" key={detail.id}>
                          <Row className="result-field-trans">
                            <Col md={8} className="result-trans-text">
                              <strong>
                                {detail.trans}{" "}
                                {!!detail.position &&
                                  detail.position !== "null" &&
                                  `(${detail.position})`}
                              </strong>
                              {detail.en_sentence && (
                                <div className="result-example">
                                  <p>
                                    -{" "}
                                    {detail.en_sentence.replaceAll(
                                      "<br />",
                                      "<br/> -"
                                    )}
                                  </p>
                                </div>
                              )}
                            </Col>
                            {detail.id &&
                              detail.review == 1 &&
                              disableSave != detail.id && (
                                <Col md={{ span: 4, offset: 0 }}>
                                  <PrimaryButton
                                    className="save-word-btn"
                                    onClick={() => {
                                      handleSaveWord(detail.id, item.content);
                                    }}
                                  >
                                    <img
                                      src={plusIcon}
                                      alt="plus icon"
                                      className="plus-icon"
                                    />
                                    <p>Lưu từ</p>
                                  </PrimaryButton>
                                </Col>
                              )}
                            {detail.id &&
                              (detail.review == 2 ||
                                wordSaved === detail.id) && (
                                <Col md={{ span: 4, offset: 0 }}>
                                  <SecondaryButton
                                    className="saved-word-btn"
                                    // onClick={() => handleSaveWord(detail.id)}
                                  >
                                    <img
                                      src={savedWordIcon}
                                      alt="plus icon"
                                      className="plus-icon"
                                    />
                                    <p>Đã lưu</p>
                                  </SecondaryButton>
                                </Col>
                              )}
                          </Row>
                        </div>
                      );
                    })
                  ) : (
                    <div className="result-field">
                      <Row className="result-field-trans">
                        <Col md={8} className="result-trans-text">
                          <strong>
                            {item.trans}{" "}
                            {!!item.position &&
                              item.position !== "null" &&
                              `(${item.position})`}
                          </strong>
                        </Col>
                        {item.id && item.review == 1 && disableSave != item.id && (
                          <Col md={{ span: 4, offset: 0 }}>
                            <PrimaryButton
                              className="save-word-btn"
                              onClick={() => {
                                handleSaveWord(item.id, item.content);
                              }}
                            >
                              <img
                                src={plusIcon}
                                alt="plus icon"
                                className="plus-icon"
                              />
                              <p>Lưu từ</p>
                            </PrimaryButton>
                          </Col>
                        )}
                        {item.id && (item.review == 2 || wordSaved == item.id) && (
                          <Col md={{ span: 4, offset: 0 }}>
                            <SecondaryButton className="saved-word-btn">
                              <img
                                src={savedWordIcon}
                                alt="plus icon"
                                className="plus-icon"
                              />
                              <p>Đã lưu</p>
                            </SecondaryButton>
                          </Col>
                        )}
                      </Row>
                    </div>
                  )}
                </div>
              );
            })}
          {!showMore && (
            <div className="">
              <h5>{vnWord[0].content}</h5>
              <div className="pronounce-field">
                <Row>
                  <Col md={2}>
                    <p className="br-title">BrE</p>
                  </Col>
                  <Col md={2}>
                    {ukSound && (
                      <img
                        src={isPlaying == "uk" ? speakerBRGif : speakerBR}
                        alt="speaker"
                        className="speaker-icon"
                        onClick={() => handlePlaySound("uk", ukSound)}
                      />
                    )}
                  </Col>
                  <Col md={8}>
                    <p className="br-pronounce">{vnWord[0].phonetic_uk}</p>
                  </Col>
                </Row>
              </div>
              <div className="pronounce-field">
                <Row>
                  <Col md={2}>
                    <p className="na-title">NAmE</p>
                  </Col>
                  <Col md={2}>
                    {usSound && (
                      <img
                        src={isPlaying == "us" ? speakerNAGif : speakerNA}
                        alt="speaker"
                        className="speaker-icon"
                        onClick={() => handlePlaySound("us", usSound)}
                      />
                    )}
                  </Col>
                  <Col md={8}>
                    <p className="br-pronounce">{vnWord[0].phonetic_us}</p>
                  </Col>
                </Row>
              </div>
              <div className="result-field">
                {vnWord[0].detail.length > 0 ? (
                  vnWord[0].detail.map((detail, index) => {
                    return (
                      <div
                        className="result-field"
                        key={detail.id}
                        style={{ display: index == 0 ? "block" : "none" }}
                      >
                        <Row className="result-field-trans">
                          <Col md={8} className="result-trans-text">
                            <strong>
                              {detail.trans}{" "}
                              {!!detail.position &&
                                detail.position !== "null" &&
                                `(${detail.position})`}
                            </strong>
                            {detail.en_sentence && (
                              <div className="result-example">
                                <p>
                                  -{" "}
                                  {detail.en_sentence.replaceAll(
                                    "<br />",
                                    "<br/> -"
                                  )}
                                </p>
                              </div>
                            )}
                          </Col>
                          {detail.id &&
                            detail.review == 1 &&
                            disableSave != detail.id && (
                              <Col md={{ span: 4, offset: 0 }}>
                                <PrimaryButton
                                  className="save-word-btn"
                                  onClick={() => {
                                    console.log("id", detail.id);
                                    console.log("text", vnWord[0].content);
                                    handleSaveWord(
                                      detail.id,
                                      vnWord[0].content
                                    );
                                  }}
                                >
                                  <img
                                    src={plusIcon}
                                    alt="plus icon"
                                    className="plus-icon"
                                  />
                                  <p>Lưu từ</p>
                                </PrimaryButton>
                              </Col>
                            )}
                          {detail.id &&
                            (detail.review == 2 || wordSaved == detail.id) && (
                              <Col md={{ span: 4, offset: 0 }}>
                                <SecondaryButton
                                  className="saved-word-btn"
                                  // onClick={() => handleSaveWord(detail.id)}
                                >
                                  <img
                                    src={savedWordIcon}
                                    alt="plus icon"
                                    className="plus-icon"
                                  />
                                  <p>Đã lưu</p>
                                </SecondaryButton>
                              </Col>
                            )}
                        </Row>
                      </div>
                    );
                  })
                ) : (
                  <div className="result-field">
                    <Row className="result-field-trans">
                      <Col md={8} className="result-trans-text">
                        <strong>
                          {vnWord[0].trans}
                          {!!vnWord[0].position &&
                            vnWord[0].position !== "null" &&
                            `(${vnWord[0].position})`}
                        </strong>
                      </Col>
                      <Col md={{ span: 4, offset: 0 }}></Col>
                    </Row>
                  </div>
                )}
              </div>
              {/* )} */}
            </div>
          )}
        </div>
        <NotifyModal
          type={showModal}
          setShowModal={(value) => setShowModal(value)}
        />
      </div>
    </>
  );
}

export default EVResult;
