/*global chrome*/
import '@splidejs/react-splide/css/skyblue';
import '@splidejs/react-splide/css/sea-green';
import { Splide, SplideSlide } from '@splidejs/react-splide';
import "../component.css";
import MainModal from "../commons/MainModal";
import cross from "../../assets/close-info.png";

function SlideModal(props) {
//   console.log("props", props);
  const {setIsSlideModal} = props;
  const handleClose = () => {
    // console.log("close",);
    props.close(false);
    setIsSlideModal(false);
  }
  const RenderSlide = JSON.parse(localStorage.getItem("dataEvent"));
  return (
    <Splide
        options={ {
        rewind: true,
        gap   : '1rem',
        } }
        aria-label="My Favorite Images"
    >
    {/* Render Ảnh slide */}
    {
        RenderSlide.map((item, index) => {
            return (
                <SplideSlide 
                    style={
                        {
                            position: 'relative',
                            zIndex: '100000',
                        }
                        } key={index}
                >
                    {/* <MainModal {...props} size="lg" className="setting-modal-wrapper"> */}
                    <a href={item.link} target="_blank">
                        <img
                        src = {item.image}
                        alt = "event"
                        className="event-modal-2"
                        style={{
                            marginTop: "3%",
                            width: "97%",
                        }}
                        />
                    </a>
                        <img
                        src={cross}
                        alt="mascot"
                        className={`modal-close-btn-2`}
                        onClick={handleClose}
                        style={
                            {
                                top: "0px",
                                right:"0px",
                            }
                        }
                        />
                    {/* </MainModal> */}
                </SplideSlide>
            )
        })
    }
    {/* {props.status ? `<div className="bgc-overlay"></div>`: ""} */}
  </Splide>
  
  );
}

export default SlideModal;
