/*global chrome*/

import "./index.css";
import speakerBR from "../../assets/speaker-br.png";
import speakerBRGif from "../../assets/speaker-gif-blue.gif";
import speakerNAGif from "../../assets/speaker-gif-red.gif";
import speakerNA from "../../assets/speaker-na.png";
import plusIcon from "../../assets/plus-icon.png";
import PrimaryButton from "../commons/PrimaryButton";
import { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Col, Row } from "react-bootstrap";
import { wordOperations } from "../../state/modules/word";
import NotifyModal from "./NotifyModal";


function EEResult({ usWord, showMore }) {
  // console.log = function () {};
  const [isPlaying,setIsPlaying] = useState("");
  const [showModal, setShowModal] = useState("");
  
const accountState = useSelector((state) => state.account);
const { userToken } = accountState;
const dispatch = useDispatch();
  const handlePlaySound = (type, sound) => {
    setIsPlaying(true);
    sound.play();
    console.log("play");
    setTimeout(() => {
      console.log("stop");
      setIsPlaying(false);

      // sound.pause();
    }, 1000);
  };
  const handleSaveWord = (wordId) => {
    setShowModal("overload");
const data = new FormData();
data.append("word_id", wordId);
data.append("user_token", userToken);
    dispatch(
      wordOperations.saveWord(data, (result) => {
        console.log({ result });
      })
    );
  };
  const ukSound = usWord[0].audio_uk && new Audio(usWord[0].audio_uk);
  const usSound = usWord[0].audio_us && new Audio(usWord[0].audio_us);
  return (
    <>
      <div className="result-wrapper en-vi-result">
        <div className="result-field-wrapper">
          {!showMore && (
            <div className="">
              <h5>{usWord[0].content}</h5>
              <h6>
                <small>
                  <i>
                    
                    {!!usWord[0].position &&
                      usWord[0].position !== "null" &&
                      `(${usWord[0].position})`}
                    
                  </i>
                </small>
              </h6>
              <div className="pronounce-field">
                <Row>
                  <Col md={2}>
                    <p className="br-title">BrE</p>
                  </Col>
                  <Col md={2}>
                    {ukSound && (
                      <img
                        src={isPlaying === "uk" ? speakerBRGif : speakerBR}
                        alt="speaker"
                        className="speaker-icon"
                        onClick={() => handlePlaySound("uk", ukSound)}
                      />
                    )}
                  </Col>
                  <Col md={8}>
                    <p className="br-pronounce">{usWord[0].phonetic_uk}</p>
                  </Col>
                </Row>
              </div>
              <div className="pronounce-field">
                <Row>
                  <Col md={2}>
                    <p className="na-title">NAmE</p>
                  </Col>
                  <Col md={2}>
                    {usSound && (
                      <img
                        src={isPlaying === "us" ? speakerNAGif : speakerNA}
                        alt="speaker"
                        className="speaker-icon"
                        onClick={() => handlePlaySound("us", usSound)}
                      />
                    )}
                  </Col>
                  <Col md={8}>
                    <p className="br-pronounce">{usWord[0].phonetic_us}</p>
                  </Col>
                </Row>
              </div>
              <div className="result-field">
                <Row className="result-field-trans">
                  <Col md={8} className="result-trans-text">
                    <strong>
                      { usWord[0].detail.length > 0 ? usWord[0].detail[0].trans.replaceAll("<br>", "<br>-") : "an (indefinite article)"}
                    </strong>
                  </Col>
                </Row>
              </div>
              {/* )} */}
            </div>
          )}
          {showMore &&
            usWord.map((item, index) => {
              const ukSoundItem = item.audio_uk && new Audio(item.audio_uk);
              const usSoundItem = item.audio_us && new Audio(item.audio_us);
              return (
                <div className="">
                  <h5>{item.content}</h5>
                  {usWord[0].position &&
                    usWord[0].position != 'null' && (
                      <h6>
                        <small>
                          <i>({usWord[0].position})</i>
                        </small>
                      </h6>
                    )}
                  <div className="pronounce-field">
                    <Row>
                      <Col md={2}>
                        <p className="br-title">BrE</p>
                      </Col>
                      <Col md={2}>
                        {ukSound && (
                          <img
                            src={
                              isPlaying == "uk" + index
                                ? speakerBRGif
                                : speakerBR
                            }
                            alt="speaker"
                            className="speaker-icon"
                            onClick={() =>
                              handlePlaySound("uk", ukSoundItem, index)
                            }
                          />
                        )}
                      </Col>
                      <Col md={8}>
                        <p className="br-pronounce">{item.phonetic_uk}</p>
                      </Col>
                    </Row>
                  </div>
                  <div className="pronounce-field">
                    <Row>
                      <Col md={2}>
                        <p className="na-title">NAmE</p>
                      </Col>
                      <Col md={2}>
                        {usSoundItem && (
                          <img
                            src={
                              isPlaying == "us" + index
                                ? speakerNAGif
                                : speakerNA
                            }
                            alt="speaker"
                            className="speaker-icon"
                            onClick={() =>
                              handlePlaySound("us", usSoundItem, index)
                            }
                          />
                        )}
                      </Col>
                      <Col md={8}>
                        <p className="br-pronounce">{item.phonetic_us}</p>
                      </Col>
                    </Row>
                  </div>
                  {item.detail.length > 0 ? (
                    item.detail.map((detail) => {
                      return (
                        <div className="result-field" key={detail.id}>
                          <Row className="result-field-trans">
                            <Col md={8} className="result-trans-text">
                              <strong>
                                {detail.trans.replaceAll("<br>", "<br>-")}
                              </strong>
                            </Col>
                          </Row>
                          {detail.en_sentence && (
                            <p
                              className="result-example"
                              dangerouslySetInnerHTML={{
                                __html:
                                  "-" +
                                  detail.en_sentence.replaceAll(
                                    "<br />",
                                    "<br/>-"
                                  ),
                              }}
                            ></p>
                          )}
                        </div>
                      );
                    })
                  ) : (
                    <div className="result-field">
                      <Row className="result-field-trans">
                        <Col md={8} className="result-trans-text">
                          <strong>
                            {item.trans}{" "}
                            {item.position && usWord[0].position != 'null'  ? '('+item.position+')' : ""}
                          </strong>
                        </Col>
                      </Row>
                    </div>
                  )}
                </div>
              );
            })}
        </div>
        <NotifyModal type={showModal} setShowModal={() => setShowModal("")} />
      </div>
    </>
  );
}

export default EEResult;
