/*global chrome*/
import { v4 as uuidv4 } from "uuid";
import LazyLoad from "react-lazyload";
import "../component.css";
import mochi from "../../assets/MOCHIMOCHI.png";
import backIcon from "../../assets/back.svg";
import mascot from "../../assets/Mochi-meomeo2.png";
import { Col, Row } from "react-bootstrap";
import SearchInput from "../commons/SearchInput";
import StartedLearn from "./StartedLearn";
import ThirdRow from "../ThirdRow/ThirdRow";
import { useState, useRef, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import mascotGuide from "../../assets/Mochi-chi-dan.png";
import MainModal from "../commons/MainModal";
import { wordOperations } from "../../state/modules/word";
import LoginModal from "../FisrtRow/LoginModal";
import ReviewWord from "./ReviewWord";
import ResultModal from "./ResultModal";
import ResultModalPara from "./ResultModalPara";
import PrimaryButton from "../commons/PrimaryButton";
import HistoryRow from "./History/HistoryRow";
import UserVideo from "./Video/UserVideo";
import ListVideo from "./Video/ListVideo";

function SecondRow({ getDataTopBar }) {
  const dispatch = useDispatch();
  const accountState = useSelector((state) => state.account);
  const wordState = useSelector((state) => state.word);
  const bookMarkState = useSelector((state) => state.bookmark);

  const { userToken, statistic, totalWord, expiredDay } = accountState;
  const { firstSearch, searchPosition } = wordState;
  const { listDataInFeed } = bookMarkState;
  // console.log("use", userToken)
  console.log("firstSearch", firstSearch);

  const [searchValue, setSearchValue] = useState("");
  const [wordSearch, setWordSearch] = useState("");
  const [wordError, setWordError] = useState("");
  const [isBannerVideo, setBannerVideo] = useState([]);
  // const [isSuccess, setSuccess] = useState(false);
  const [listDataInFeedLocal, setListDataInFeedLocal] = useState([]);

  const [showResult, setShowResult] = useState(false);
  const [showResultParagraph, setShowResultParagraph] = useState(false);
  const [showUpgrade, setShowUpgrade] = useState(false);

  const [showLogin, setShowLogin] = useState(false);

  chrome.storage.sync.get(["gaTracking"], function (result) {
    if (result.gaTracking !== undefined) {
      setMyuuid(result.gaTracking);
    }
  });

  const [myuuid, setMyuuid] = useState("");

  useEffect(() => {
    if (firstSearch === true) {
      chrome.runtime.sendMessage("searchFirstDay");
    }
  }, [firstSearch]);

  const handleFirstSearchDay = () => {
    //Tracking lần đăng nhập đầu tiên trong ngày
    // let clickSearchCurrent = new Date().toDateString();
    // let timeStampClick = new Date(clickSearchCurrent).getTime();
    // chrome.runtime.sendMessage("getTimeSave", function (response) {
    //   let currentTime = response;
    //   let currentTimeLocal = new Date(currentTime);
    //   let timeStampSearch = currentTimeLocal.getTime();
    //   if (timeStampSearch !== timeStampClick) {
    //     chrome.runtime.sendMessage(
    //       {
    //         name: "saveFirstSearch",
    //         data: clickSearchCurrent,
    //       },
    //       function (response) {
    //         if (response) {
    //           chrome.runtime.sendMessage("searchFirstDay");
    //         }
    //       }
    //     );
    //   }
    // });
    chrome.storage.sync.get(["gaTracking"], function (result) {
      console.log("result", result.gaTracking);
      if (result.gaTracking !== undefined) {
        setMyuuid(result.gaTracking);
        dispatch(
          wordOperations.searchWord(
            {
              key: wordSearch,
              user_token: userToken,
              uuid: myuuid,
              search_positions: "first_search",
            },
            (result) => {
              if (result) {
                setShowResult(true);
              }
            }
          )
        );
      } else if (result.gaTracking === undefined) {
        let newuuidEnter = uuidv4();
        setMyuuid(newuuidEnter);
        chrome.storage.sync.set({ gaTracking: newuuidEnter }, function () {});
        dispatch(
          wordOperations.searchWord(
            {
              key: wordSearch,
              user_token: userToken,
              uuid: myuuid,
              search_positions: "first_search",
            },
            (result) => {
              if (result) {
                setShowResult(true);
              }
            }
          )
        );
      }
    });
  };

  const handleChangeSearch = (e) => {
    const value = e.target.value;
    setSearchValue(value);
  };
  const handleSearchSuggest = (word) => {
    chrome.storage.sync.get(["gaTracking"], function (result) {
      if (result.gaTracking !== undefined) {
        setMyuuid(result.gaTracking);
        dispatch(
          wordOperations.searchWord(
            {
              key: word,
              user_token: userToken,
              uuid: myuuid,
              search_positions: "first_search",
            },
            (result) => {
              if (result) {
                setShowResult(true);
              }
            }
          )
        );
      } else if (result.gaTracking === undefined) {
        let newuuid = uuidv4();
        setMyuuid(newuuid);
        dispatch(
          wordOperations.searchWord(
            {
              key: word,
              user_token: userToken,
              uuid: myuuid,
              search_positions: "first_search",
            },
            (result) => {
              if (result) {
                setShowResult(true);
              }
            }
          )
        );
      }
    });
    setWordSearch(word);
  };
  const handleClickSearch = () => {
    window.open("https://www.google.com/search?q=" + searchValue, "_self");
  };
  const handlePress = (e) => {
    if (e.which == 13) {
      window.open("https://www.google.com/search?q=" + searchValue, "_self");
    }
  };
  const handleWordPress = (e) => {
    if (e.which == 13) {
      // handleFirstSearchDay();
      setShowResult(false);
      setShowResultParagraph(false);
      const isValidWord = checkSpellWord(wordSearch);
      // console.log("isValidWord", isValidWord);
      if (!isValidWord) {
        setWordError("Vui lòng nhập từ tiếng Anh!");
      } else {
        if (wordSearch.length < 30) {
          handleFirstSearchDay();
        } else {
          if (expiredDay !== "") {
            setShowResultParagraph(true);
            dispatch(
              wordOperations.searchParagraph({ q: wordSearch }, (result) => {
                if (result) {
                }
              })
            );
          } else {
            setShowUpgrade(true);
          }
        }
      }
    }
  };
  const handleFocus = (e) => {
    // console.log("use", + userToken);
    // console.log("local",!userToken && window.localStorage.getItem("profileToken") == null)
    if (window.localStorage.getItem("profileToken") == null && !userToken) {
      chrome.runtime.sendMessage("modalLogin");
      // console.log("object");
      setShowLogin(true);
      // console.log('id', e.target );
      document.getElementById(e.target.id).blur();
    }
  };
  const handleInputSearch = (e) => {
    const value = e.target.value;
    setWordSearch(value);
    setWordError("");
    if (value == "") {
      setShowResult(false);
    }
  };
  const handleSearchWord = () => {
    // handleFirstSearchDay();
    setShowResult(false);
    setShowResultParagraph(false);

    const isValidWord = checkSpellWord(wordSearch);
    if (!isValidWord) {
      setWordError("Vui lòng nhập từ tiếng Anh!");
    } else {
      if (wordSearch.length < 30) {
        handleFirstSearchDay();
        // dispatch(
        //   wordOperations.searchWord(
        //     { key: wordSearch, user_token: userToken },
        //     (result) => {
        //       if (result) {
        //         setShowResult(true);
        //       }
        //     }
        //   )
        // );
      } else {
        if (expiredDay !== "") {
          setShowResultParagraph(true);
          dispatch(
            wordOperations.searchParagraph({ q: wordSearch }, (result) => {
              if (result) {
              }
            })
          );
        } else {
          setShowUpgrade(true);
        }
      }
    }
  };
  const checkSpellWord = (word) => {
    var testUnsignedCharacter =
      /[àáãạảăắằẳẵặâấầẩẫậèéẹẻẽêềếểễệđìíĩỉịòóõọỏôốồổỗộơớờởỡợùúũụủưứừửữựỳỵỷỹýÀÁÃẠẢĂẮẰẲẴẶÂẤẦẨẪẬÈÉẸẺẼÊỀẾỂỄỆĐÌÍĨỈỊÒÓÕỌỎÔỐỒỔỖỘƠỚỜỞỠỢÙÚŨỤỦƯỨỪỬỮỰỲỴỶỸÝ]+/g;
    const regexForeign = /[A-z\u00C0-\u00ff]+/g;
    const convertWord = word.replace(/[^a-zA-Z ]/g, "").trim();
    console.log(!testUnsignedCharacter.test(word));
    if (
      regexForeign.test(word) &&
      convertWord !== "" &&
      !testUnsignedCharacter.test(word)
    ) {
      return true;
    } else {
      return false;
    }
  };
  // const handleBackToGoogle = () => {
  //   chrome.tabs.update({
  //     url: "chrome-search://local-ntp/local-ntp.html",
  //   });
  //   fetch("https://script.google.com/macros/s/AKfycbwycss-_sBwggp8bEYDkcEXWmCXfQDu3TcwYN8nZpA1m0q-nZUtS7qEXi54SiIu9IDK-Q/exec?sheetName=back_chrome_tab");
  //   window.open("https://mochidemy.com/extension-page-login/?disableNT=1");
  // };
  const handleTracking = (index, position) => {
    console.log("----index----", index);
    if (typeof index === "number" && position === "bookMark") {
      chrome.runtime.sendMessage(`clickBookMark${index}`);
    } else if (index === "searchBar") {
      chrome.runtime.sendMessage("clickSearchBar");
    } else if (typeof index === "number" && position === "infeed") {
      chrome.runtime.sendMessage(`clickInFeed${index}`);
    }
  };

  useEffect(() => {
    if (
      Array.isArray(listDataInFeed) &&
      listDataInFeed.length <= 0 &&
      localStorage.getItem("bookMarkState") !== null
    ) {
      let getDataInFeed = JSON.parse(
        localStorage.getItem("bookMarkState")
      ).listDataInFeed;
      setListDataInFeedLocal(getDataInFeed);
    }
  }, []);
  return (
    <>
      <Row className="second-row" id="ext__scroll">
        <Col lg={{ span: 3 }}></Col>
        <Col md={{ span: 8 }} lg={{ span: 6 }} className="second-row-left">
          <Row>
            <Col md={{ span: 8, offset: 2 }}>
              <div className="google-search-input-wrapper">
                <img
                  src={mochi}
                  className="mochi-search-title"
                  alt="mochi-search-title"
                />
                <SearchInput
                  placeholder="Nhập nội dung bạn muốn tìm kiếm"
                  className="google-search-input"
                  onChange={handleChangeSearch}
                  onKeyPress={handlePress}
                  value={searchValue}
                  onClick={handleClickSearch}
                />
                {/* <div
                  className="google-back-wrapper blur-background"
                  onClick={handleBackToGoogle}
                >
                  <img src={backIcon} alt="back" />
                  <p>Quay lại giao diện Chrome</p>
                </div> */}
                <UserVideo
                  isBannerVideo={isBannerVideo}
                  handleTracking={handleTracking}
                />
              </div>
            </Col>
          </Row>
          <Row>
            <div
              className="list__video--mochi"
              style={{
                marginTop: "31px",
              }}
            >
              {(listDataInFeed.length > 0
                ? listDataInFeed
                : listDataInFeedLocal
              ).map((item, index) => {
                return (
                  <>
                    <ListVideo
                      urlImage={item.image}
                      urlText={item.text}
                      link={item.link}
                      index={index}
                      handleTracking={handleTracking}
                    />
                  </>
                );
              })}
            </div>
          </Row>
        </Col>
        <Col
          md={{ span: 4, offset: 0 }}
          lg={{ span: 3, offset: 0 }}
          className="button-group-wrapper"
        >
          <div className="search-word-wrapper right-wrapper blur-background">
            <p className="search-title">MOCHI DICTIONARY</p>
            <SearchInput
              placeholder="Gõ vào đây từ bạn muốn tra cứu"
              id="lookup-input"
              onFocus={handleFocus}
              onChange={handleInputSearch}
              onClick={handleSearchWord}
              onKeyPress={handleWordPress}
              value={wordSearch}
            />
            {wordError && <small className="word-error"> {wordError}</small>}
          </div>
          {showResult && (
            <ResultModal
              setShowResult={setShowResult}
              handleSearchSuggest={handleSearchSuggest}
            />
          )}
          {showResultParagraph && (
            <ResultModalPara setShowResultParagraph={setShowResultParagraph} />
          )}

          <div className="information-wrapper right-wrapper blur-background">
            <div className="information-title">
              <p>MOCHI - LEARN ENGLISH</p>
            </div>
            {userToken && totalWord > 0 ? (
              <ReviewWord
                totalWord={totalWord}
                statistic={statistic}
              ></ReviewWord>
            ) : (
              <StartedLearn />
            )}
          </div>
        </Col>
      </Row>

      <MainModal
        mascot={mascot}
        show={showUpgrade}
        handleClose={() => setShowUpgrade(false)}
      >
        <div className="forget-notice-wrapper">
          <p className="boldText">
            Nâng cấp tài khoản để mở khoá tất cả các tính năng của Mochi
            DIctionary
          </p>
          <PrimaryButton
            onClick={() => {
              chrome.runtime.sendMessage("checkClickUpgrade");
              window.open(
                "https://mochidemy.com/extension?source=extension&category=extension_browser&action=click_button&label=upgrade_premium",
                "_blank"
              );
            }}
            className="inbox-button"
          >
            Nâng cấp ngay
          </PrimaryButton>
        </div>
      </MainModal>
      <Row style={{ width: "77%", position: "fixed", bottom: 10 }}>
        <Col md={{ span: 12 }}>
          <ThirdRow />
        </Col>
      </Row>
      <LoginModal
        mascot={mascotGuide}
        show={showLogin}
        handleClose={() => setShowLogin(false)}
      ></LoginModal>
    </>
  );
}

export default SecondRow;
