/*global chrome*/

import "../component.css";
import MainModal from "../commons/MainModal";
import PrimaryButton from "../commons/PrimaryButton";
import SecondaryButton from "../commons/SecondaryButton";
import { useEffect } from "react";

const ORIGIN = process.env.REACT_APP_ORIGIN;
const trackingURL = process.env.REACT_APP_TRACKING_URL;

function LoginModal(props) {
  function getCookies(domain, name, callback) {
    chrome.cookies.get({ url: domain, name: name }, function (cookie) {
      if (callback && cookie) {
        callback(cookie.value);
      }
    });
  }
  const handleOpenSignUp = async () => {
    if(window.localStorage.getItem("buttonLogin") == "1"){
      chrome.runtime.sendMessage("clickButtonSignUpinNewTab");
      window.localStorage.removeItem("buttonLogin");
    } else {
      chrome.runtime.sendMessage("newTabSignUp");
    }
    
    window.open(
      "https://mochidemy.com/extension-page-login/register?" 
    );
    const signInInterval = setInterval(() => {
      getCookies(ORIGIN, "mochiToken", function (cookie) {
        // console.log({ cookie });
        if (cookie) {
          clearInterval(signInInterval);
          window.location.reload();
        }
        // setDisableNewTab(mochiToken);
      });
    }, 1000);
}
useEffect(() => {
},[]);
  return (
    <MainModal {...props}>
      <div className="login-notify">
        <p>
          <strong>
            Đăng nhập tài khoản học Mochi để mở khoá tất cả các tính năng
          </strong>
        </p>
        <div className="login-notify-group-btn">
          <PrimaryButton
            onClick={() =>
              {
                if(window.localStorage.getItem("buttonLogin") == "1"){
                  chrome.runtime.sendMessage("clickButtonLoginNewTab");
                  window.localStorage.removeItem("buttonLogin");
                } else {
                  chrome.runtime.sendMessage("newTabLogin");
                }
                window.open("https://mochidemy.com/extension-page-login/login?");
                const signInInterval = setInterval(() => {
                getCookies(ORIGIN, "mochiToken", function (cookie) {
                  // console.log({ cookie });
                  if (cookie) {
                    clearInterval(signInInterval);
                    window.location.reload();
                    
                  }
                  // setDisableNewTab(mochiToken);
                });
              }, 1000);}
            }
          >
            Đăng nhập
          </PrimaryButton>
          <SecondaryButton
            onClick={handleOpenSignUp}
          >
            {" "}
            Tạo tài khoản mới
          </SecondaryButton>
        </div>
      </div>
    </MainModal>
  );
}

export default LoginModal;
