/*global chrome*/

import "../component.css";
import mochiStarted from '../../assets/Mochi1.png'
import { Col, Row } from "react-bootstrap";
import PrimaryButton from "../commons/PrimaryButton";
import ReactGA from "react-ga";
import { useSelector,useDispatch } from "react-redux";
const trackingURL = process.env.REACT_APP_TRACKING_URL;

function StartedLearn() {
//   ReactGA.initialize("UA-150440250-2", { debug: true });
// const ga = ReactGA.ga();
// ga("set", "checkProtocolTask", function () {
//   /* nothing */
// });
  const { email } = useSelector((state) => state.account);
  
  const handleOpenWebsite = async () => {
    window.open("https://learn.mochidemy.com?source=extension&category=extension_browser&action=click_button&label=learn_new_words","_blank");
  }
  return (
    <div className="information-started">
      <img src={mochiStarted} alt="mochi-mascot" className="mascot-started" />
      <p className="starter-CTA">Ghi nhớ 1000 từ vựng trong 1 tháng với MochiMochi</p>
      <PrimaryButton
        onClick={handleOpenWebsite}
        className="learn-now-btn"
      >
        Học ngay thôi
      </PrimaryButton>
    </div>
  );
}

export default StartedLearn;
